import { useState, useEffect, useCallback } from 'react';
import { supabase } from '../supabaseClient';

/**
 * useAdminAuth
 *
 * Manages auth state + community role for the admin panel.
 * Returns: { user, community, communityRole, loading, error, login, register, logout }
 */
export function useAdminAuth(communitySlug) {
  const [user, setUser] = useState(null);
  const [community, setCommunity] = useState(null);
  const [communityRole, setCommunityRole] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Fetch community membership for the given user
  const fetchCommunityRole = useCallback(async (userId) => {
    if (!communitySlug) {
      setError('No community specified');
      setLoading(false);
      return;
    }

    try {
      // Step 1: Look up community by slug
      const { data: communityData, error: communityError } = await supabase
        .from('communities')
        .select('*')
        .eq('slug', communitySlug)
        .single();

      if (communityError || !communityData) {
        setCommunity(null);
        setCommunityRole(null);
        setLoading(false);
        return;
      }

      setCommunity(communityData);

      // Step 2: Look up membership by user_id + community_id
      const { data: memberData, error: memberError } = await supabase
        .from('community_members')
        .select('role')
        .eq('user_id', userId)
        .eq('community_id', communityData.id)
        .single();

      if (memberError || !memberData) {
        setCommunityRole(null);
      } else {
        setCommunityRole(memberData.role);
      }
    } catch (err) {
      console.error('[AdminAuth] Failed to fetch community role:', err);
      setError('Failed to verify community access');
    }
    setLoading(false);
  }, [communitySlug]);

  // Check session on mount + listen for auth changes
  useEffect(() => {
    let cancelled = false;

    supabase.auth.getSession().then(({ data: { session } }) => {
      if (cancelled) return;
      const currentUser = session?.user || null;
      setUser(currentUser);
      if (currentUser) {
        fetchCommunityRole(currentUser.id);
      } else {
        setLoading(false);
      }
    });

    const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
      if (cancelled) return;
      const currentUser = session?.user || null;
      setUser(currentUser);
      if (currentUser) {
        fetchCommunityRole(currentUser.id);
      } else {
        setCommunity(null);
        setCommunityRole(null);
        setLoading(false);
      }
    });

    return () => {
      cancelled = true;
      subscription.unsubscribe();
    };
  }, [fetchCommunityRole]);

  // Login with email/password
  const login = useCallback(async (email, password) => {
    setError(null);
    const { error: loginError } = await supabase.auth.signInWithPassword({ email, password });
    if (loginError) {
      if (loginError.message === 'Invalid login credentials') {
        throw new Error('Incorrect email or password.');
      }
      throw loginError;
    }
  }, []);

  // Register new account
  const register = useCallback(async (email, password, username) => {
    setError(null);
    const { data, error: regError } = await supabase.auth.signUp({
      email,
      password,
      options: {
        data: { username },
      },
    });
    if (regError) throw regError;

    if (data?.user?.identities?.length === 0) {
      throw new Error('An account with this email already exists.');
    }

    return data;
  }, []);

  // Logout
  const logout = useCallback(async () => {
    await supabase.auth.signOut();
    setUser(null);
    setCommunity(null);
    setCommunityRole(null);
  }, []);

  return { user, community, communityRole, loading, error, login, register, logout };
}
