import React, { useState } from 'react';
import { hasCommunityRole } from './permissions';
import DashboardTab from './tabs/DashboardTab';
import QuestionsTab from './tabs/QuestionsTab';
import ThemeTab from './tabs/ThemeTab';
import AnalyticsTab from './tabs/AnalyticsTab';
import SettingsTab from './tabs/SettingsTab';

const TABS = [
  { id: 'dashboard', label: 'Dashboard', icon: '\u{1F4CA}', minRole: 'moderator' },
  { id: 'questions', label: 'Questions', icon: '\u{2753}', minRole: 'moderator' },
  { id: 'theme', label: 'Theme & Behavior', icon: '\u{1F3A8}', minRole: 'commissioner' },
  { id: 'analytics', label: 'Analytics', icon: '\u{1F4C8}', minRole: 'moderator' },
  { id: 'settings', label: 'Settings', icon: '\u{2699}\u{FE0F}', minRole: 'commissioner' },
];

const TAB_COMPONENTS = {
  dashboard: DashboardTab,
  questions: QuestionsTab,
  theme: ThemeTab,
  analytics: AnalyticsTab,
  settings: SettingsTab,
};

/**
 * AdminShell  Main admin layout with sidebar navigation and content area.
 */
function AdminShell({ user, community, communityRole, onLogout }) {
  const visibleTabs = TABS.filter(tab => hasCommunityRole(communityRole, tab.minRole));
  const [activeTab, setActiveTab] = useState(visibleTabs[0]?.id || 'dashboard');
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [toast, setToast] = useState(null);

  const showToast = (msg, type = 'success') => {
    setToast({ msg, type });
    setTimeout(() => setToast(null), 3000);
  };

  const ActiveComponent = TAB_COMPONENTS[activeTab];
  const userDisplay = user?.user_metadata?.username || user?.email || 'User';

  return (
    <div style={{
      display: 'flex',
      minHeight: '100vh',
      fontFamily: 'system-ui, -apple-system, sans-serif',
    }}>
      {/* Mobile hamburger */}
      <button
        onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
        style={{
          display: 'none',
          position: 'fixed',
          top: '12px',
          left: '12px',
          zIndex: 1001,
          background: '#041E42',
          color: '#FFFFFF',
          border: 'none',
          borderRadius: '6px',
          padding: '8px 12px',
          fontSize: '1.25rem',
          cursor: 'pointer',
          fontFamily: 'inherit',
          ...mobileStyles.hamburgerButton,
        }}
        aria-label="Toggle menu"
      >
        {mobileMenuOpen ? '\u2715' : '\u2630'}
      </button>

      {/* Sidebar */}
      <nav style={{
        width: '240px',
        minWidth: '240px',
        backgroundColor: '#041E42',
        color: '#FFFFFF',
        display: 'flex',
        flexDirection: 'column',
        position: 'fixed',
        top: 0,
        left: 0,
        bottom: 0,
        zIndex: 1000,
        transition: 'transform 0.2s ease',
        ...mobileStyles.sidebar(mobileMenuOpen),
      }}>
        {/* Community header */}
        <div style={{ padding: '24px 20px 16px', borderBottom: '1px solid rgba(255,255,255,0.1)' }}>
          <div style={{
            fontSize: '0.6875rem',
            textTransform: 'uppercase',
            letterSpacing: '1.5px',
            opacity: 0.6,
            marginBottom: '6px',
          }}>
            Embed Admin
          </div>
          <div style={{ fontSize: '1rem', fontWeight: 600, lineHeight: 1.3 }}>
            {community.name}
          </div>
        </div>

        {/* Tab list */}
        <div style={{ flex: 1, padding: '8px 0', overflowY: 'auto' }}>
          {visibleTabs.map(tab => {
            const isActive = tab.id === activeTab;
            return (
              <button
                key={tab.id}
                onClick={() => { setActiveTab(tab.id); setMobileMenuOpen(false); }}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                  gap: '10px',
                  width: '100%',
                  padding: '12px 20px',
                  border: 'none',
                  borderLeft: isActive ? '3px solid #4A90D9' : '3px solid transparent',
                  backgroundColor: isActive ? '#0A3A7A' : 'transparent',
                  color: '#FFFFFF',
                  fontSize: '0.875rem',
                  fontWeight: isActive ? 500 : 400,
                  cursor: 'pointer',
                  textAlign: 'left',
                  fontFamily: 'inherit',
                  transition: 'background-color 0.15s ease',
                }}
                onMouseEnter={(e) => {
                  if (!isActive) e.currentTarget.style.backgroundColor = '#0A2A5A';
                }}
                onMouseLeave={(e) => {
                  if (!isActive) e.currentTarget.style.backgroundColor = 'transparent';
                }}
              >
                <span style={{ fontSize: '1rem' }}>{tab.icon}</span>
                <span>{tab.label}</span>
              </button>
            );
          })}
        </div>

        {/* User footer */}
        <div style={{
          padding: '16px 20px',
          borderTop: '1px solid rgba(255,255,255,0.1)',
        }}>
          <div style={{
            fontSize: '0.8125rem',
            marginBottom: '4px',
            overflow: 'hidden',
            textOverflow: 'ellipsis',
            whiteSpace: 'nowrap',
          }}>
            {userDisplay}
          </div>
          <div style={{
            fontSize: '0.6875rem',
            opacity: 0.5,
            marginBottom: '10px',
            textTransform: 'capitalize',
          }}>
            {communityRole}
          </div>
          <button
            onClick={onLogout}
            style={{
              background: 'none',
              border: '1px solid rgba(255,255,255,0.25)',
              color: '#FFFFFF',
              padding: '6px 14px',
              borderRadius: '6px',
              fontSize: '0.75rem',
              cursor: 'pointer',
              fontFamily: 'inherit',
              opacity: 0.8,
              transition: 'opacity 0.15s ease',
            }}
            onMouseEnter={(e) => { e.currentTarget.style.opacity = '1'; }}
            onMouseLeave={(e) => { e.currentTarget.style.opacity = '0.8'; }}
          >
            Sign out
          </button>
        </div>
      </nav>

      {/* Mobile overlay */}
      {mobileMenuOpen && (
        <div
          onClick={() => setMobileMenuOpen(false)}
          style={{
            position: 'fixed',
            inset: 0,
            backgroundColor: 'rgba(0,0,0,0.4)',
            zIndex: 999,
            display: 'none',
            ...mobileStyles.overlay,
          }}
        />
      )}

      {/* Content area */}
      <main style={{
        flex: 1,
        marginLeft: '240px',
        backgroundColor: '#F5F5F7',
        minHeight: '100vh',
        padding: '32px',
        boxSizing: 'border-box',
        ...mobileStyles.content,
      }}>
        {ActiveComponent && (
          <ActiveComponent
            community={community}
            communityRole={communityRole}
            onToast={showToast}
            onNavigate={(tab) => setActiveTab(tab)}
          />
        )}
      </main>

      {/* Toast notification */}
      {toast && (
        <div style={{
          position: 'fixed',
          top: '20px',
          right: '20px',
          padding: '12px 20px',
          borderRadius: '8px',
          fontSize: '14px',
          fontWeight: 500,
          zIndex: 2000,
          boxShadow: '0 4px 12px rgba(0,0,0,0.15)',
          background: toast.type === 'error' ? '#FEE2E2' : '#D1FAE5',
          color: toast.type === 'error' ? '#991B1B' : '#065F46',
        }}>
          {toast.msg}
        </div>
      )}

      {/* Inject mobile media query via style tag */}
      <style>{`
        @media (max-width: 768px) {
          [data-admin-hamburger] { display: block !important; }
          [data-admin-sidebar] { transform: translateX(${mobileMenuOpen ? '0' : '-100%'}) !important; }
          [data-admin-overlay] { display: block !important; }
          [data-admin-content] { margin-left: 0 !important; padding: 60px 16px 16px !important; }
        }
      `}</style>
    </div>
  );
}

// Media query workaround for inline styles
// These get spread into components but the real responsive behavior
// is handled by the <style> tag injected above using data attributes.
// For initial render, we use JS-based media query detection.
const isMobile = typeof window !== 'undefined' && window.innerWidth <= 768;

const mobileStyles = {
  hamburgerButton: isMobile ? { display: 'block' } : {},
  sidebar: (open) => isMobile ? { transform: open ? 'translateX(0)' : 'translateX(-100%)' } : {},
  overlay: isMobile ? { display: 'block' } : {},
  content: isMobile ? { marginLeft: 0, padding: '60px 16px 16px' } : {},
};

export default AdminShell;
