import React, { useState, useEffect, useCallback } from 'react';
import Papa from 'papaparse';
import { canManageQuestions } from './permissions';

/**
 * QuestionManager  Shared question CRUD component for trivia communities.
 *
 * Features: Fetch, search, filter (category/difficulty/tags/source), pagination,
 * select/bulk-select, add question modal, delete (single + bulk), CSV import
 * (PapaParse + preview), CSV export/template, tag management, expandable detail rows.
 *
 * Props:
 *   communityId        community UUID
 *   supabaseClient     Supabase client instance (passed in)
 *   userRole           community role string
 *   onToast            (msg, type) => void notification callback
 *   theme              optional color tokens object
 *   communityCategories  optional string[] for category dropdown
 *   extraActionButtons  optional ReactNode injected into action bar
 *   onQuestionsChange  optional (questions[]) => void notifies host when questions change
 */

const DEFAULT_THEME = {
  primary: '#041E42',
  accent: '#4A90D9',
  bg: '#FFFFFF',
  surface: '#F5F5F7',
  text: '#1a1a1a',
  textMuted: '#54585A',
  border: '#E5E7EB',
  danger: '#DC2626',
  success: '#059669',
};

/*  tiny inline SVG icons (no external deps)  */
const Icon = ({ d, size = 14, color = 'currentColor', style }) => (
  <svg width={size} height={size} viewBox="0 0 24 24" fill="none" stroke={color} strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" style={{ display: 'inline-block', verticalAlign: 'middle', ...style }}>
    <path d={d} />
  </svg>
);
const PlusIcon = (p) => <Icon d="M12 5v14M5 12h14" {...p} />;
const UploadIcon = (p) => <Icon d="M21 15v4a2 2 0 01-2 2H5a2 2 0 01-2-2v-4M17 8l-5-5-5 5M12 3v12" {...p} />;
const DownloadIcon = (p) => <Icon d="M21 15v4a2 2 0 01-2 2H5a2 2 0 01-2-2v-4M7 10l5 5 5-5M12 15V3" {...p} />;
const TagIcon = (p) => <Icon d="M20.59 13.41l-7.17 7.17a2 2 0 01-2.83 0L2 12V2h10l8.59 8.59a2 2 0 010 2.82zM7 7h.01" {...p} />;
const ChevronIcon = (p) => <Icon d="M6 9l6 6 6-6" {...p} />;
const SearchIcon = (p) => <Icon d="M11 17.25a6.25 6.25 0 110-12.5 6.25 6.25 0 010 12.5zM16 16l4.5 4.5" {...p} />;

function QuestionManager({
  communityId,
  supabaseClient,
  userRole,
  onToast,
  theme: themeProp,
  communityCategories,
  extraActionButtons,
  onQuestionsChange,
}) {
  const t = { ...DEFAULT_THEME, ...themeProp };

  //  state 
  const [questions, setQuestions] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [filterCategory, setFilterCategory] = useState('all');
  const [filterDifficulty, setFilterDifficulty] = useState('all');
  const [filterSource, setFilterSource] = useState('all');
  const [selectedTags, setSelectedTags] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageSize, setPageSize] = useState(25);
  const [expandedQuestionId, setExpandedQuestionId] = useState(null);
  const [selectedQuestions, setSelectedQuestions] = useState([]);
  const [selectAllPages, setSelectAllPages] = useState(false);
  const [showBulkTagging, setShowBulkTagging] = useState(false);
  const [bulkTagInput, setBulkTagInput] = useState('');
  const [editingQuestionId, setEditingQuestionId] = useState(null);
  const [newTag, setNewTag] = useState('');
  const [activeModal, setActiveModal] = useState(null); // 'add' | 'import' | null

  // CSV state
  const [csvData, setCsvData] = useState([]);
  const [csvPreview, setCsvPreview] = useState([]);
  const [csvErrors, setCsvErrors] = useState([]);
  const [uploading, setUploading] = useState(false);

  // Add Question form
  const [addQForm, setAddQForm] = useState({
    question_text: '', correct_answer: '', incorrect_1: '', incorrect_2: '', incorrect_3: '',
    category: '', difficulty: 'medium', tags: '', explanation: '',
  });
  const [addQErrors, setAddQErrors] = useState([]);
  const [addQSubmitting, setAddQSubmitting] = useState(false);

  //  fetch 
  const fetchQuestions = useCallback(async () => {
    try {
      const { data } = await supabaseClient
        .from('community_questions')
        .select('*')
        .eq('community_id', communityId)
        .order('created_at', { ascending: false });
      const qs = data || [];
      setQuestions(qs);
      if (onQuestionsChange) onQuestionsChange(qs);
    } catch (err) {
      console.error('QuestionManager: fetch error', err);
    }
    setLoading(false);
  }, [communityId, supabaseClient, onQuestionsChange]);

  useEffect(() => { fetchQuestions(); }, [fetchQuestions]);

  // Reset to page 1 when filters change
  useEffect(() => { setCurrentPage(1); }, [searchQuery, filterCategory, filterDifficulty, filterSource, selectedTags, pageSize]);

  // Lock body scroll when modal open
  useEffect(() => {
    if (activeModal) {
      document.body.style.overflow = 'hidden';
      const handleEsc = (e) => { if (e.key === 'Escape') setActiveModal(null); };
      document.addEventListener('keydown', handleEsc);
      return () => { document.body.style.overflow = ''; document.removeEventListener('keydown', handleEsc); };
    } else {
      document.body.style.overflow = '';
    }
  }, [activeModal]);

  //  derived 
  const filteredQuestions = questions.filter(q => {
    const categoryMatch = filterCategory === 'all' || q.category === filterCategory;
    const difficultyMatch = filterDifficulty === 'all' || q.difficulty === filterDifficulty;
    const searchMatch = !searchQuery ||
      q.question_text.toLowerCase().includes(searchQuery.toLowerCase()) ||
      q.correct_answer.toLowerCase().includes(searchQuery.toLowerCase()) ||
      q.incorrect_answers.some(a => a.toLowerCase().includes(searchQuery.toLowerCase())) ||
      q.category.toLowerCase().includes(searchQuery.toLowerCase());
    const tagMatch = selectedTags.length === 0 || (q.tags && selectedTags.every(tag => q.tags.includes(tag)));
    const sourceMatch = filterSource === 'all'
      || (filterSource === 'manual' && !q.source && !q.imported_at)
      || (filterSource === 'csv' && q.imported_at && q.source !== 'ai_generated')
      || (filterSource === 'ai' && (q.source === 'ai_generated' || (q.tags && q.tags.includes('ai-generated'))));
    return categoryMatch && difficultyMatch && searchMatch && tagMatch && sourceMatch;
  });

  const totalPages = Math.max(1, Math.ceil(filteredQuestions.length / pageSize));
  const safeCurrentPage = Math.min(currentPage, totalPages);
  const pagedQuestions = filteredQuestions.slice((safeCurrentPage - 1) * pageSize, safeCurrentPage * pageSize);
  const pageStart = filteredQuestions.length === 0 ? 0 : (safeCurrentPage - 1) * pageSize + 1;
  const pageEnd = Math.min(safeCurrentPage * pageSize, filteredQuestions.length);

  const categories = [...new Set(questions.map(q => q.category))];
  const allTags = [...new Set(questions.flatMap(q => q.tags || []))];

  const getSourceLabel = (q) => {
    if (q.source === 'ai_generated' || (q.tags && q.tags.includes('ai-generated'))) return 'AI';
    if (q.imported_at && q.source !== 'ai_generated') return 'CSV';
    return 'Manual';
  };

  //  delete 
  const handleDeleteQuestion = async (questionId) => {
    if (!window.confirm('Are you sure you want to delete this question?')) return;
    try {
      const { error } = await supabaseClient.from('community_questions').delete().eq('id', questionId).eq('community_id', communityId);
      if (error) { onToast('Failed to delete question: ' + error.message, 'error'); } else { fetchQuestions(); }
    } catch (err) { console.error('Error deleting question:', err); }
  };

  const handleBulkDelete = async () => {
    if (selectedQuestions.length === 0) { onToast('No questions selected', 'error'); return; }
    if (!window.confirm(`Are you sure you want to delete ${selectedQuestions.length} questions?`)) return;
    try {
      const { error } = await supabaseClient.from('community_questions').delete().in('id', selectedQuestions).eq('community_id', communityId);
      if (error) { onToast('Failed to delete questions: ' + error.message, 'error'); }
      else { setSelectedQuestions([]); setSelectAllPages(false); fetchQuestions(); }
    } catch (err) { console.error('Error deleting questions:', err); }
  };

  //  selection 
  const toggleQuestionSelection = (id) => { setSelectAllPages(false); setSelectedQuestions(p => p.includes(id) ? p.filter(x => x !== id) : [...p, id]); };
  const toggleSelectAll = () => {
    const ids = pagedQuestions.map(q => q.id);
    const allSel = ids.length > 0 && ids.every(id => selectedQuestions.includes(id));
    if (allSel) { setSelectedQuestions(p => p.filter(id => !ids.includes(id))); setSelectAllPages(false); }
    else { setSelectedQuestions(p => [...new Set([...p, ...ids])]); }
  };
  const handleSelectAllPages = () => { setSelectAllPages(true); setSelectedQuestions(filteredQuestions.map(q => q.id)); };
  const handleDeselectAll = () => { setSelectedQuestions([]); setSelectAllPages(false); setShowBulkTagging(false); };

  //  tags 
  const handleAddTag = async (questionId, tag) => {
    if (!tag.trim()) return;
    const question = questions.find(q => q.id === questionId);
    const currentTags = question.tags || [];
    if (currentTags.includes(tag.trim())) { onToast('Tag already exists on this question', 'error'); return; }
    const updatedTags = [...currentTags, tag.trim()];
    const { error } = await supabaseClient.from('community_questions').update({ tags: updatedTags }).eq('id', questionId).eq('community_id', communityId);
    if (error) { onToast('Failed to add tag: ' + error.message, 'error'); } else { setNewTag(''); fetchQuestions(); }
  };

  const handleRemoveTag = async (questionId, tag) => {
    const question = questions.find(q => q.id === questionId);
    const updatedTags = (question.tags || []).filter(t => t !== tag);
    const { error } = await supabaseClient.from('community_questions').update({ tags: updatedTags }).eq('id', questionId).eq('community_id', communityId);
    if (error) { onToast('Failed to remove tag: ' + error.message, 'error'); } else { fetchQuestions(); }
  };

  const toggleTagFilter = (tag) => { setSelectedTags(p => p.includes(tag) ? p.filter(t => t !== tag) : [...p, tag]); };

  const handleBulkAddTag = async () => {
    if (selectedQuestions.length === 0) { onToast('No questions selected', 'error'); return; }
    if (!bulkTagInput.trim()) { onToast('Please enter a tag name', 'error'); return; }
    const tag = bulkTagInput.trim();
    try {
      const toUpdate = selectedQuestions.map(id => questions.find(q => q.id === id)).filter(q => q && !(q.tags || []).includes(tag));
      if (toUpdate.length === 0) { onToast('All selected questions already have this tag', 'error'); return; }
      const results = await Promise.all(toUpdate.map(q =>
        supabaseClient.from('community_questions').update({ tags: [...(q.tags || []), tag] }).eq('id', q.id).eq('community_id', communityId)
      ));
      const failures = results.filter(r => r.error);
      if (failures.length > 0) { onToast(`Tagged ${results.length - failures.length} questions. ${failures.length} failed.`, 'error'); }
      else { onToast(`Tag "${tag}" added to ${results.length} questions`); }
      setBulkTagInput(''); setShowBulkTagging(false); fetchQuestions();
    } catch (err) { onToast('Failed to add tags: ' + err.message, 'error'); }
  };

  const handleBulkRemoveTag = async (tag) => {
    if (selectedQuestions.length === 0) { onToast('No questions selected', 'error'); return; }
    if (!window.confirm(`Remove tag "${tag}" from ${selectedQuestions.length} selected questions?`)) return;
    try {
      const toUpdate = selectedQuestions.map(id => questions.find(q => q.id === id)).filter(q => q && (q.tags || []).includes(tag));
      if (toUpdate.length === 0) { onToast('No selected questions have this tag', 'error'); return; }
      const results = await Promise.all(toUpdate.map(q =>
        supabaseClient.from('community_questions').update({ tags: (q.tags || []).filter(tg => tg !== tag) }).eq('id', q.id).eq('community_id', communityId)
      ));
      const failures = results.filter(r => r.error);
      if (failures.length > 0) { onToast(`Removed tag from ${results.length - failures.length} questions. ${failures.length} failed.`, 'error'); }
      else { onToast(`Tag "${tag}" removed from ${results.length} questions`); }
      fetchQuestions();
    } catch (err) { onToast('Failed to remove tags: ' + err.message, 'error'); }
  };

  //  CSV 
  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    if (!file) return;
    if (!file.name.endsWith('.csv')) { onToast('Please upload a CSV file', 'error'); return; }
    if (file.size > 1 * 1024 * 1024) { onToast('CSV file must be under 1 MB', 'error'); return; }
    Papa.parse(file, {
      header: true,
      skipEmptyLines: true,
      complete: (results) => { validateAndPreviewCSV(results.data); },
      error: (error) => { onToast('Error parsing CSV: ' + error.message, 'error'); }
    });
  };

  const validateAndPreviewCSV = (data) => {
    const MAX_ROWS = 500;
    const errors = [];
    const validQuestions = [];
    const required = ['question_text', 'correct_answer', 'incorrect_answer_1', 'incorrect_answer_2', 'incorrect_answer_3', 'category', 'difficulty'];
    if (data.length > MAX_ROWS) { setCsvErrors([`CSV exceeds the ${MAX_ROWS}-row limit (${data.length} rows). Split into smaller files.`]); setCsvData([]); setCsvPreview([]); return; }
    if (data.length > 0) {
      const cols = Object.keys(data[0]);
      const missing = required.filter(c => !cols.includes(c));
      if (missing.length > 0) { errors.push(`Missing required columns: ${missing.join(', ')}`); setCsvErrors(errors); setCsvData([]); setCsvPreview([]); return; }
    }
    data.forEach((row, idx) => {
      const re = [];
      if (!row.question_text?.trim()) re.push(`Row ${idx + 1}: Missing question text`);
      if (!row.correct_answer?.trim()) re.push(`Row ${idx + 1}: Missing correct answer`);
      if (!row.incorrect_answer_1?.trim() || !row.incorrect_answer_2?.trim() || !row.incorrect_answer_3?.trim()) re.push(`Row ${idx + 1}: Missing incorrect answers (need 3)`);
      if (!row.category?.trim()) re.push(`Row ${idx + 1}: Missing category`);
      if (!row.difficulty || !['easy', 'medium', 'hard'].includes(row.difficulty.toLowerCase())) re.push(`Row ${idx + 1}: Invalid difficulty`);
      if (re.length > 0) { errors.push(...re); } else {
        const q = { question_text: row.question_text.trim(), correct_answer: row.correct_answer.trim(), incorrect_answers: [row.incorrect_answer_1.trim(), row.incorrect_answer_2.trim(), row.incorrect_answer_3.trim()], category: row.category.trim(), difficulty: row.difficulty.toLowerCase().trim() };
        if (row.image_url?.trim()) q.image_url = row.image_url.trim();
        if (row.video_url?.trim()) q.video_url = row.video_url.trim();
        if (row.explanation?.trim()) q.explanation = row.explanation.trim();
        validQuestions.push(q);
      }
    });
    setCsvErrors(errors); setCsvData(validQuestions); setCsvPreview(validQuestions.slice(0, 5));
  };

  const handleBulkImport = async () => {
    if (csvData.length === 0) { onToast('No valid questions to import', 'error'); return; }
    if (!window.confirm(`Are you sure you want to import ${csvData.length} questions?`)) return;
    setUploading(true);
    try {
      const ts = new Date().toISOString();
      const rows = csvData.map(q => {
        const r = { community_id: communityId, question_text: q.question_text, correct_answer: q.correct_answer, incorrect_answers: q.incorrect_answers, category: q.category, difficulty: q.difficulty, created_at: ts, imported_at: ts };
        if (q.image_url) r.image_url = q.image_url;
        if (q.video_url) r.video_url = q.video_url;
        if (q.explanation) r.explanation = q.explanation;
        return r;
      });
      const { error } = await supabaseClient.from('community_questions').insert(rows);
      if (error) { onToast('Failed to import questions: ' + error.message, 'error'); }
      else { onToast(`Successfully imported ${csvData.length} questions!`); setCsvData([]); setCsvPreview([]); setCsvErrors([]); setActiveModal(null); fetchQuestions(); }
    } catch (err) { onToast('Failed to import questions', 'error'); }
    setUploading(false);
  };

  const downloadTemplate = () => {
    const tpl = `question_text,correct_answer,incorrect_answer_1,incorrect_answer_2,incorrect_answer_3,category,difficulty,image_url,video_url,explanation
"What is the capital of France?","Paris","London","Berlin","Madrid","Geography","easy","","","Paris has been the capital since the 10th century."
"Who painted the Mona Lisa?","Leonardo da Vinci","Michelangelo","Raphael","Donatello","Art","medium","","","Leonardo painted the Mona Lisa between 1503 and 1519."`;
    const blob = new Blob([tpl], { type: 'text/csv' });
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a'); a.href = url; a.download = 'question_template.csv'; a.click();
    window.URL.revokeObjectURL(url);
  };

  const sanitizeCSVField = (value) => {
    const str = String(value ?? '');
    const escaped = str.replace(/"/g, '""');
    const dangerous = /^[=+\-@\t\r]/;
    const safe = dangerous.test(escaped) ? "'" + escaped : escaped;
    return `"${safe}"`;
  };

  const exportToCSV = () => {
    if (questions.length === 0) { onToast('No questions to export', 'error'); return; }
    const csvRows = ['question_text,correct_answer,incorrect_answer_1,incorrect_answer_2,incorrect_answer_3,category,difficulty,explanation'];
    questions.forEach(q => {
      csvRows.push([
        sanitizeCSVField(q.question_text), sanitizeCSVField(q.correct_answer),
        sanitizeCSVField(q.incorrect_answers[0] || ''), sanitizeCSVField(q.incorrect_answers[1] || ''), sanitizeCSVField(q.incorrect_answers[2] || ''),
        sanitizeCSVField(q.category), sanitizeCSVField(q.difficulty), sanitizeCSVField(q.explanation || '')
      ].join(','));
    });
    const blob = new Blob([csvRows.join('\n')], { type: 'text/csv' });
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a'); a.href = url; a.download = `questions_${new Date().toISOString().split('T')[0]}.csv`; a.click();
    window.URL.revokeObjectURL(url);
  };

  //  add question 
  const handleAddQuestion = async () => {
    const resolvedCategory = addQForm.category === '__other__' ? (addQForm._customCategory || '').trim() : addQForm.category.trim();
    const errors = [];
    if (!addQForm.question_text.trim()) errors.push('Question text is required');
    if (!addQForm.correct_answer.trim()) errors.push('Correct answer is required');
    if (!addQForm.incorrect_1.trim()) errors.push('Incorrect answer 1 is required');
    if (!addQForm.incorrect_2.trim()) errors.push('Incorrect answer 2 is required');
    if (!addQForm.incorrect_3.trim()) errors.push('Incorrect answer 3 is required');
    if (!resolvedCategory) errors.push('Category is required');
    if (!addQForm.difficulty) errors.push('Difficulty is required');
    setAddQErrors(errors);
    if (errors.length > 0) return;
    setAddQSubmitting(true);
    try {
      const tags = addQForm.tags.trim() ? addQForm.tags.split(',').map(tg => tg.trim()).filter(Boolean) : [];
      const row = {
        community_id: communityId, question_text: addQForm.question_text.trim(), correct_answer: addQForm.correct_answer.trim(),
        incorrect_answers: [addQForm.incorrect_1.trim(), addQForm.incorrect_2.trim(), addQForm.incorrect_3.trim()],
        category: resolvedCategory, difficulty: addQForm.difficulty, source: 'manual', version_number: 1, version_history: [], tags,
      };
      if (addQForm.explanation.trim()) row.explanation = addQForm.explanation.trim();
      const { error } = await supabaseClient.from('community_questions').insert([row]);
      if (error) throw error;
      setAddQForm({ question_text: '', correct_answer: '', incorrect_1: '', incorrect_2: '', incorrect_3: '', category: '', difficulty: 'medium', tags: '', explanation: '' });
      setAddQErrors([]); setActiveModal(null); onToast('Question added!'); fetchQuestions();
    } catch (err) { onToast('Failed to add question: ' + err.message, 'error'); }
    setAddQSubmitting(false);
  };

  //  permission gate 
  if (!canManageQuestions(userRole)) {
    return <div style={{ padding: '24px', color: t.textMuted, textAlign: 'center' }}>You do not have permission to manage questions.</div>;
  }

  if (loading) {
    return <div style={{ padding: '24px', color: t.textMuted, textAlign: 'center' }}>Loading questions...</div>;
  }

  /*  STYLES  */
  const s = {
    actionBar: { display: 'flex', justifyContent: 'space-between', alignItems: 'center', flexWrap: 'wrap', gap: '12px', marginBottom: '16px' },
    actionTitle: { fontSize: '1.25rem', fontWeight: 600, color: t.primary, margin: 0 },
    actionBtns: { display: 'flex', gap: '8px', flexWrap: 'wrap' },
    actionBtn: { display: 'inline-flex', alignItems: 'center', gap: '5px', padding: '7px 14px', fontSize: '13px', fontWeight: 500, border: `1px solid ${t.border}`, borderRadius: '6px', background: t.bg, color: t.text, cursor: 'pointer', fontFamily: 'inherit', transition: 'background 0.15s' },
    section: { background: t.bg, borderRadius: '8px', padding: '20px', border: `1px solid ${t.border}`, marginBottom: '16px' },
    searchWrap: { position: 'relative', marginBottom: '12px' },
    searchInput: { width: '100%', padding: '9px 12px 9px 34px', borderRadius: '6px', border: `1px solid ${t.border}`, fontSize: '14px', boxSizing: 'border-box', fontFamily: 'inherit', outline: 'none' },
    searchIconWrap: { position: 'absolute', left: '10px', top: '50%', transform: 'translateY(-50%)', pointerEvents: 'none', color: t.textMuted },
    clearBtn: { position: 'absolute', right: '8px', top: '50%', transform: 'translateY(-50%)', background: 'none', border: 'none', cursor: 'pointer', fontSize: '16px', color: t.textMuted, padding: '2px 6px' },
    filterRow: { display: 'flex', gap: '12px', flexWrap: 'wrap', alignItems: 'center', marginBottom: '12px' },
    filterGroup: { display: 'flex', flexDirection: 'column', gap: '3px' },
    filterLabel: { fontSize: '11px', fontWeight: 600, color: t.textMuted, textTransform: 'uppercase', letterSpacing: '0.5px' },
    filterSelect: { padding: '6px 10px', borderRadius: '6px', border: `1px solid ${t.border}`, fontSize: '13px', fontFamily: 'inherit', background: t.bg, color: t.text },
    filterResults: { fontSize: '13px', color: t.textMuted, marginLeft: 'auto' },
    tagFilterRow: { display: 'flex', flexWrap: 'wrap', gap: '6px', alignItems: 'center', marginBottom: '8px' },
    tagBtn: (active) => ({ padding: '4px 10px', borderRadius: '12px', border: `1px solid ${active ? t.accent : t.border}`, background: active ? t.accent : t.bg, color: active ? '#fff' : t.text, fontSize: '12px', cursor: 'pointer', fontFamily: 'inherit', fontWeight: active ? 500 : 400 }),
    sourceRow: { display: 'flex', gap: '6px', flexWrap: 'wrap', alignItems: 'center', marginBottom: '12px' },
    // table
    tableHeader: { display: 'flex', alignItems: 'center', padding: '10px 12px', background: t.surface, borderRadius: '6px 6px 0 0', borderBottom: `1px solid ${t.border}`, fontSize: '12px', fontWeight: 600, color: t.textMuted, textTransform: 'uppercase', letterSpacing: '0.5px', gap: '8px' },
    row: (selected, alt, expanded) => ({ display: 'flex', alignItems: 'center', padding: '10px 12px', borderBottom: `1px solid ${t.border}`, background: selected ? '#EBF5FF' : alt ? t.surface : t.bg, cursor: 'pointer', gap: '8px', transition: 'background 0.1s', ...(expanded ? { borderBottom: 'none' } : {}) }),
    colCheck: { width: '32px', flexShrink: 0, display: 'flex', alignItems: 'center' },
    colQuestion: { flex: 3, minWidth: 0, overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap', fontSize: '13px', color: t.text },
    colAnswer: { flex: 2, minWidth: 0, overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap', fontSize: '13px', color: t.textMuted },
    colCat: { flex: 1, minWidth: '80px' },
    colDiff: { flex: 0.7, minWidth: '60px' },
    colSource: { flex: 0.7, minWidth: '50px' },
    colExpand: { width: '28px', flexShrink: 0, display: 'flex', alignItems: 'center', justifyContent: 'center' },
    chip: { display: 'inline-block', padding: '2px 8px', borderRadius: '10px', fontSize: '11px', fontWeight: 500, background: '#EEF1F5', color: t.text },
    diffChip: (diff) => ({ display: 'inline-block', padding: '2px 8px', borderRadius: '10px', fontSize: '11px', fontWeight: 500, background: diff === 'easy' ? '#D1FAE5' : diff === 'medium' ? '#FEF3C7' : '#FEE2E2', color: diff === 'easy' ? '#065F46' : diff === 'medium' ? '#92400E' : '#991B1B' }),
    sourceChip: (src) => ({ display: 'inline-block', padding: '2px 8px', borderRadius: '10px', fontSize: '11px', fontWeight: 500, background: src === 'AI' ? '#EDE9FE' : src === 'CSV' ? '#DBEAFE' : '#F3F4F6', color: src === 'AI' ? '#5B21B6' : src === 'CSV' ? '#1E40AF' : '#374151' }),
    // expand panel
    expandPanel: { padding: '16px 16px 16px 52px', borderBottom: `1px solid ${t.border}`, background: '#FAFBFC' },
    expandQuestion: { fontSize: '14px', fontWeight: 500, marginBottom: '10px', color: t.text, lineHeight: 1.5 },
    answerPill: (correct) => ({ display: 'inline-block', padding: '4px 10px', borderRadius: '14px', fontSize: '12px', marginRight: '6px', marginBottom: '4px', fontWeight: 500, background: correct ? '#D1FAE5' : '#FEE2E2', color: correct ? '#065F46' : '#991B1B' }),
    expandTagRow: { display: 'flex', flexWrap: 'wrap', gap: '6px', alignItems: 'center', marginTop: '10px' },
    questionTag: { display: 'inline-flex', alignItems: 'center', gap: '4px', padding: '3px 8px', borderRadius: '10px', background: '#EEF1F5', fontSize: '11px', fontWeight: 500, color: t.text },
    removeTagBtn: { background: 'none', border: 'none', cursor: 'pointer', fontSize: '13px', color: t.textMuted, padding: '0 2px', lineHeight: 1 },
    addTagTrigger: { padding: '3px 8px', borderRadius: '10px', border: `1px dashed ${t.border}`, background: 'none', fontSize: '11px', cursor: 'pointer', color: t.accent, fontFamily: 'inherit' },
    tagInputWrap: { display: 'flex', gap: '4px', alignItems: 'center' },
    tagInput: { padding: '3px 8px', borderRadius: '6px', border: `1px solid ${t.border}`, fontSize: '12px', width: '100px', fontFamily: 'inherit' },
    tagSmBtn: (danger) => ({ padding: '3px 8px', borderRadius: '6px', border: 'none', fontSize: '11px', fontWeight: 500, cursor: 'pointer', fontFamily: 'inherit', background: danger ? '#FEE2E2' : '#EEF1F5', color: danger ? '#991B1B' : t.text }),
    explanationRow: { marginTop: '8px', padding: '8px 12px', background: '#FFFBEB', borderRadius: '6px', fontSize: '13px', color: '#92400E', lineHeight: 1.4 },
    expandFooter: { display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '12px', flexWrap: 'wrap', gap: '8px' },
    expandMeta: { display: 'flex', gap: '12px', fontSize: '12px', color: t.textMuted },
    expandActions: { display: 'flex', gap: '8px' },
    dangerSmBtn: { padding: '4px 10px', borderRadius: '6px', border: 'none', fontSize: '12px', fontWeight: 500, cursor: 'pointer', fontFamily: 'inherit', background: '#FEE2E2', color: '#991B1B' },
    // select-all banner
    banner: { padding: '8px 12px', background: '#EBF5FF', borderBottom: `1px solid ${t.border}`, fontSize: '13px', color: t.accent, textAlign: 'center' },
    bannerLink: { background: 'none', border: 'none', color: t.accent, fontWeight: 600, cursor: 'pointer', textDecoration: 'underline', fontFamily: 'inherit', fontSize: '13px' },
    // pagination
    pagination: { display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '12px 0', flexWrap: 'wrap', gap: '8px' },
    pageInfo: { fontSize: '13px', color: t.textMuted },
    pageControls: { display: 'flex', alignItems: 'center', gap: '8px' },
    pageBtn: (disabled) => ({ padding: '6px 12px', borderRadius: '6px', border: `1px solid ${t.border}`, background: t.bg, fontSize: '13px', cursor: disabled ? 'default' : 'pointer', opacity: disabled ? 0.5 : 1, fontFamily: 'inherit', color: t.text }),
    pageNum: { fontSize: '13px', color: t.text, fontWeight: 500 },
    pageSizeSelect: { padding: '5px 8px', borderRadius: '6px', border: `1px solid ${t.border}`, fontSize: '13px', fontFamily: 'inherit', background: t.bg },
    // bulk bar
    bulkBar: { position: 'fixed', bottom: '20px', left: '50%', transform: 'translateX(-50%)', background: t.primary, color: '#fff', padding: '12px 20px', borderRadius: '10px', display: 'flex', alignItems: 'center', gap: '12px', zIndex: 100, boxShadow: '0 4px 20px rgba(0,0,0,0.25)', flexWrap: 'wrap' },
    bulkCount: { fontSize: '13px', fontWeight: 600, whiteSpace: 'nowrap' },
    bulkBtn: { padding: '6px 12px', borderRadius: '6px', border: 'none', fontSize: '12px', fontWeight: 500, cursor: 'pointer', fontFamily: 'inherit', background: 'rgba(255,255,255,0.2)', color: '#fff', display: 'inline-flex', alignItems: 'center', gap: '4px' },
    bulkBtnDanger: { padding: '6px 12px', borderRadius: '6px', border: 'none', fontSize: '12px', fontWeight: 500, cursor: 'pointer', fontFamily: 'inherit', background: t.danger, color: '#fff' },
    bulkBtnGhost: { padding: '6px 12px', borderRadius: '6px', border: '1px solid rgba(255,255,255,0.3)', fontSize: '12px', fontWeight: 500, cursor: 'pointer', fontFamily: 'inherit', background: 'transparent', color: '#fff' },
    bulkTagInput: { display: 'flex', gap: '4px', alignItems: 'center' },
    bulkTagInputEl: { padding: '5px 8px', borderRadius: '6px', border: 'none', fontSize: '12px', width: '120px', fontFamily: 'inherit' },
    bulkRemoveTag: { padding: '4px 8px', borderRadius: '10px', border: '1px solid rgba(255,255,255,0.3)', background: 'transparent', color: '#fff', fontSize: '11px', cursor: 'pointer', fontFamily: 'inherit' },
    // modals
    overlay: { position: 'fixed', inset: 0, background: 'rgba(0,0,0,0.5)', zIndex: 200, display: 'flex', alignItems: 'center', justifyContent: 'center', padding: '20px' },
    modal: { background: t.bg, borderRadius: '12px', width: '100%', maxWidth: '640px', maxHeight: '85vh', overflow: 'auto', boxShadow: '0 20px 60px rgba(0,0,0,0.3)' },
    modalHeader: { display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '16px 20px', borderBottom: `1px solid ${t.border}` },
    modalTitle: { fontSize: '1.1rem', fontWeight: 600, color: t.primary, margin: 0 },
    modalClose: { background: 'none', border: 'none', fontSize: '22px', cursor: 'pointer', color: t.textMuted, padding: '4px 8px', lineHeight: 1 },
    modalBody: { padding: '20px' },
    // form fields
    fieldLabel: { display: 'block', fontSize: '13px', fontWeight: 600, marginBottom: '4px', color: t.text },
    fieldRequired: { color: t.danger },
    fieldInput: { width: '100%', padding: '8px 10px', borderRadius: '6px', border: `1px solid ${t.border}`, fontSize: '14px', boxSizing: 'border-box', fontFamily: 'inherit', outline: 'none' },
    fieldTextarea: { width: '100%', padding: '8px 10px', borderRadius: '6px', border: `1px solid ${t.border}`, fontSize: '14px', boxSizing: 'border-box', fontFamily: 'inherit', resize: 'vertical', outline: 'none' },
    fieldSelect: { width: '100%', padding: '8px 10px', borderRadius: '6px', border: `1px solid ${t.border}`, fontSize: '14px', boxSizing: 'border-box', fontFamily: 'inherit', background: t.bg },
    fieldGroup: { marginBottom: '14px' },
    fieldRow3: { display: 'grid', gridTemplateColumns: '1fr 1fr 1fr', gap: '12px', marginBottom: '14px' },
    fieldRow2: { display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '12px', marginBottom: '14px' },
    submitBtn: { width: '100%', padding: '10px', borderRadius: '6px', border: 'none', fontSize: '14px', fontWeight: 600, cursor: 'pointer', fontFamily: 'inherit', background: t.primary, color: '#fff', marginTop: '8px' },
    errorBox: { background: '#FEE2E2', padding: '10px 14px', borderRadius: '6px', marginBottom: '14px', fontSize: '13px', color: '#991B1B' },
    // import modal extras
    uploadInstructions: { marginBottom: '16px', fontSize: '13px', color: t.textMuted, lineHeight: 1.5 },
    csvErrorBox: { background: '#FEE2E2', padding: '12px 14px', borderRadius: '6px', marginTop: '12px', fontSize: '13px', color: '#991B1B' },
    previewTable: { width: '100%', borderCollapse: 'collapse', fontSize: '12px', marginTop: '8px' },
    previewTh: { textAlign: 'left', padding: '6px 8px', borderBottom: `2px solid ${t.border}`, fontWeight: 600, color: t.textMuted },
    previewTd: { padding: '6px 8px', borderBottom: `1px solid ${t.border}`, maxWidth: '200px', overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' },
    emptyMsg: { padding: '40px 20px', textAlign: 'center', color: t.textMuted, fontSize: '14px' },
    // media hints in expand
    mediaThumbs: { display: 'flex', gap: '8px', marginTop: '8px' },
    mediaThumb: { height: '80px', borderRadius: '6px', objectFit: 'cover' },
  };

  /*  RENDER  */
  return (
    <div style={{ fontFamily: 'system-ui, -apple-system, sans-serif' }}>
      {/* Action Bar */}
      <div style={s.actionBar}>
        <h2 style={s.actionTitle}>Questions ({questions.length})</h2>
        <div style={s.actionBtns}>
          <button style={s.actionBtn} onClick={() => setActiveModal('add')}><PlusIcon size={14} /> Add</button>
          <button style={s.actionBtn} onClick={() => setActiveModal('import')}><UploadIcon size={14} /> Import CSV</button>
          {extraActionButtons}
          <button style={{ ...s.actionBtn, opacity: questions.length === 0 ? 0.5 : 1 }} onClick={exportToCSV} disabled={questions.length === 0}><DownloadIcon size={14} /> Export</button>
        </div>
      </div>

      {/* Search + Filters + Table */}
      <div style={s.section}>
        {questions.length > 0 && (
          <>
            {/* Search */}
            <div style={s.searchWrap}>
              <div style={s.searchIconWrap}><SearchIcon size={16} /></div>
              <input style={s.searchInput} placeholder="Search questions, answers, or categories..." value={searchQuery} onChange={e => setSearchQuery(e.target.value)} />
              {searchQuery && <button style={s.clearBtn} onClick={() => setSearchQuery('')}>&times;</button>}
            </div>

            {/* Category + Difficulty filters */}
            <div style={s.filterRow}>
              <div style={s.filterGroup}>
                <span style={s.filterLabel}>Category</span>
                <select style={s.filterSelect} value={filterCategory} onChange={e => setFilterCategory(e.target.value)}>
                  <option value="all">All Categories</option>
                  {categories.map(c => <option key={c} value={c}>{c}</option>)}
                </select>
              </div>
              <div style={s.filterGroup}>
                <span style={s.filterLabel}>Difficulty</span>
                <select style={s.filterSelect} value={filterDifficulty} onChange={e => setFilterDifficulty(e.target.value)}>
                  <option value="all">All Difficulties</option>
                  <option value="easy">Easy</option>
                  <option value="medium">Medium</option>
                  <option value="hard">Hard</option>
                </select>
              </div>
              <div style={s.filterResults}>Showing {filteredQuestions.length} of {questions.length}</div>
            </div>

            {/* Tag filters */}
            {allTags.length > 0 && (
              <div style={{ marginBottom: '8px' }}>
                <span style={{ fontSize: '11px', fontWeight: 600, color: t.textMuted, marginRight: '8px' }}>Tags:</span>
                <div style={s.tagFilterRow}>
                  {allTags.map(tag => (
                    <button key={tag} style={s.tagBtn(selectedTags.includes(tag))} onClick={() => toggleTagFilter(tag)}>
                      {tag} {selectedTags.includes(tag) && '\u2713'}
                    </button>
                  ))}
                  {selectedTags.length > 0 && (
                    <button style={{ ...s.tagBtn(false), color: t.danger, borderColor: t.danger }} onClick={() => setSelectedTags([])}>Clear All</button>
                  )}
                </div>
              </div>
            )}

            {/* Source filters */}
            <div style={s.sourceRow}>
              <span style={{ fontSize: '11px', fontWeight: 600, color: t.textMuted, marginRight: '4px' }}>Source:</span>
              {[{ v: 'all', l: 'All' }, { v: 'manual', l: 'Manual' }, { v: 'csv', l: 'CSV Import' }, { v: 'ai', l: 'AI Generated' }].map(opt => (
                <button key={opt.v} style={s.tagBtn(filterSource === opt.v)} onClick={() => setFilterSource(opt.v)}>
                  {opt.l} {filterSource === opt.v && '\u2713'}
                </button>
              ))}
            </div>
          </>
        )}

        {questions.length === 0 ? (
          <div style={s.emptyMsg}>No questions in the question bank yet. Use the buttons above to add questions.</div>
        ) : (
          <div>
            {/* Table Header */}
            <div style={s.tableHeader}>
              <div style={s.colCheck}>
                <input type="checkbox" checked={pagedQuestions.length > 0 && pagedQuestions.every(q => selectedQuestions.includes(q.id))} onChange={toggleSelectAll} />
              </div>
              <div style={s.colQuestion}>Question</div>
              <div style={s.colAnswer}>Answer</div>
              <div style={s.colCat}>Category</div>
              <div style={s.colDiff}>Difficulty</div>
              <div style={s.colSource}>Source</div>
              <div style={s.colExpand}></div>
            </div>

            {/* Select-all banners */}
            {pagedQuestions.length > 0 && pagedQuestions.every(q => selectedQuestions.includes(q.id)) && !selectAllPages && filteredQuestions.length > pageSize && (
              <div style={s.banner}>
                All {pagedQuestions.length} on this page selected.{' '}
                <button style={s.bannerLink} onClick={handleSelectAllPages}>Select all {filteredQuestions.length} questions across all pages</button>
              </div>
            )}
            {selectAllPages && (
              <div style={s.banner}>
                All {filteredQuestions.length} questions selected.{' '}
                <button style={s.bannerLink} onClick={handleDeselectAll}>Clear selection</button>
              </div>
            )}

            {/* Rows */}
            {pagedQuestions.map((question, idx) => {
              const isExpanded = expandedQuestionId === question.id;
              const isSelected = selectedQuestions.includes(question.id);
              const srcLabel = getSourceLabel(question);
              return (
                <React.Fragment key={question.id}>
                  <div
                    style={s.row(isSelected, idx % 2 === 1, isExpanded)}
                    onClick={(e) => {
                      if (e.target.closest('[data-qm-check]') || e.target.tagName === 'INPUT') return;
                      setExpandedQuestionId(isExpanded ? null : question.id);
                    }}
                  >
                    <div style={s.colCheck} data-qm-check onClick={e => e.stopPropagation()}>
                      <input type="checkbox" checked={isSelected} onChange={() => toggleQuestionSelection(question.id)} />
                    </div>
                    <div style={s.colQuestion}>{question.question_text}</div>
                    <div style={s.colAnswer}>{question.correct_answer}</div>
                    <div style={s.colCat}><span style={s.chip}>{question.category}</span></div>
                    <div style={s.colDiff}><span style={s.diffChip(question.difficulty)}>{question.difficulty}</span></div>
                    <div style={s.colSource}><span style={s.sourceChip(srcLabel)}>{srcLabel}</span></div>
                    <div style={s.colExpand}>
                      <span style={{ transform: isExpanded ? 'rotate(180deg)' : 'rotate(0deg)', transition: 'transform 0.2s', display: 'inline-block' }}>
                        <ChevronIcon size={14} color={t.textMuted} />
                      </span>
                    </div>
                  </div>

                  {/* Expanded panel */}
                  {isExpanded && (
                    <div style={s.expandPanel}>
                      <div style={s.expandQuestion}>{question.question_text}</div>

                      {/* Answers */}
                      <div>
                        <span style={s.answerPill(true)}>{question.correct_answer}</span>
                        {question.incorrect_answers?.map((ans, i) => (
                          <span key={i} style={s.answerPill(false)}>{ans}</span>
                        ))}
                      </div>

                      {/* Tags */}
                      <div style={s.expandTagRow}>
                        {question.tags && question.tags.length > 0 && question.tags.map(tag => (
                          <span key={tag} style={s.questionTag}>
                            {tag}
                            <button style={s.removeTagBtn} onClick={() => handleRemoveTag(question.id, tag)}>&times;</button>
                          </span>
                        ))}
                        {editingQuestionId === question.id ? (
                          <div style={s.tagInputWrap}>
                            <input style={s.tagInput} placeholder="Add tag..." value={newTag} onChange={e => setNewTag(e.target.value)} onKeyPress={e => { if (e.key === 'Enter') handleAddTag(question.id, newTag); }} />
                            <button style={s.tagSmBtn(false)} onClick={() => handleAddTag(question.id, newTag)}>Add</button>
                            <button style={s.tagSmBtn(true)} onClick={() => { setEditingQuestionId(null); setNewTag(''); }}>Cancel</button>
                          </div>
                        ) : (
                          <button style={s.addTagTrigger} onClick={() => setEditingQuestionId(question.id)}>+ Add Tag</button>
                        )}
                      </div>

                      {/* Explanation */}
                      {question.explanation && (
                        <div style={s.explanationRow}>{question.explanation}</div>
                      )}

                      {/* Media thumbnails */}
                      {(question.image_url || question.video_url) && (
                        <div style={s.mediaThumbs}>
                          {question.image_url && <img src={question.image_url} alt="Question" style={s.mediaThumb} />}
                          {question.video_url && (() => {
                            const m = question.video_url.match(/(?:youtube\.com\/watch\?v=|youtu\.be\/|youtube\.com\/embed\/)([a-zA-Z0-9_-]{11})/);
                            return m ? <img src={`https://img.youtube.com/vi/${m[1]}/mqdefault.jpg`} alt="Video" style={s.mediaThumb} /> : null;
                          })()}
                        </div>
                      )}

                      {/* Footer */}
                      <div style={s.expandFooter}>
                        <div style={s.expandMeta}>
                          <span>Added {new Date(question.created_at).toLocaleDateString()}</span>
                          <span>Source: {srcLabel}</span>
                          {question.version_number > 0 && <span>v{question.version_number}</span>}
                        </div>
                        <div style={s.expandActions}>
                          <button style={s.dangerSmBtn} onClick={() => handleDeleteQuestion(question.id)}>Delete</button>
                        </div>
                      </div>
                    </div>
                  )}
                </React.Fragment>
              );
            })}

            {/* Pagination */}
            {filteredQuestions.length > 0 && (
              <div style={s.pagination}>
                <div style={s.pageInfo}>Showing {pageStart}&ndash;{pageEnd} of {filteredQuestions.length} questions</div>
                <div style={s.pageControls}>
                  <button style={s.pageBtn(safeCurrentPage <= 1)} disabled={safeCurrentPage <= 1} onClick={() => setCurrentPage(p => p - 1)}>Previous</button>
                  <span style={s.pageNum}>Page {safeCurrentPage} of {totalPages}</span>
                  <button style={s.pageBtn(safeCurrentPage >= totalPages)} disabled={safeCurrentPage >= totalPages} onClick={() => setCurrentPage(p => p + 1)}>Next</button>
                </div>
                <select style={s.pageSizeSelect} value={pageSize} onChange={e => setPageSize(Number(e.target.value))}>
                  <option value={25}>25 per page</option>
                  <option value={50}>50 per page</option>
                  <option value={100}>100 per page</option>
                </select>
              </div>
            )}

            {/* Floating bulk actions bar */}
            {selectedQuestions.length > 0 && (
              <div style={s.bulkBar}>
                <span style={s.bulkCount}>{selectedQuestions.length} selected</span>
                <button style={s.bulkBtn} onClick={() => setShowBulkTagging(!showBulkTagging)}><TagIcon size={13} color="#fff" /> Add Tag</button>
                {showBulkTagging && (
                  <div style={s.bulkTagInput} onClick={e => e.stopPropagation()}>
                    <input style={s.bulkTagInputEl} placeholder="Tag name..." value={bulkTagInput} onChange={e => setBulkTagInput(e.target.value)} onKeyPress={e => { if (e.key === 'Enter') handleBulkAddTag(); }} />
                    <button style={{ ...s.bulkBtn, background: 'rgba(255,255,255,0.3)' }} onClick={handleBulkAddTag}>Add</button>
                  </div>
                )}
                {allTags.length > 0 && (
                  <div style={{ display: 'flex', gap: '4px', flexWrap: 'wrap' }}>
                    {allTags.slice(0, 5).map(tag => (
                      <button key={tag} style={s.bulkRemoveTag} onClick={() => handleBulkRemoveTag(tag)} title={`Remove "${tag}" from selected`}>{tag} &times;</button>
                    ))}
                  </div>
                )}
                <button style={s.bulkBtnDanger} onClick={handleBulkDelete}>Delete Selected</button>
                <button style={s.bulkBtnGhost} onClick={handleDeselectAll}>Deselect All</button>
              </div>
            )}
          </div>
        )}
      </div>

      {/* ===== ADD QUESTION MODAL ===== */}
      {activeModal === 'add' && (
        <div style={s.overlay} onClick={() => setActiveModal(null)}>
          <div style={s.modal} onClick={e => e.stopPropagation()}>
            <div style={s.modalHeader}>
              <h2 style={s.modalTitle}>Add New Question</h2>
              <button style={s.modalClose} onClick={() => setActiveModal(null)}>&times;</button>
            </div>
            <div style={s.modalBody}>
              {addQErrors.length > 0 && (
                <div style={s.errorBox}>{addQErrors.map((e, i) => <p key={i} style={{ margin: '2px 0' }}>{e}</p>)}</div>
              )}
              <div style={s.fieldGroup}>
                <label style={s.fieldLabel}>Question Text <span style={s.fieldRequired}>*</span></label>
                <textarea style={s.fieldTextarea} rows={3} value={addQForm.question_text} onChange={e => setAddQForm(f => ({ ...f, question_text: e.target.value }))} placeholder="Enter your trivia question..." />
              </div>
              <div style={s.fieldGroup}>
                <label style={s.fieldLabel}>Correct Answer <span style={s.fieldRequired}>*</span></label>
                <input style={s.fieldInput} value={addQForm.correct_answer} onChange={e => setAddQForm(f => ({ ...f, correct_answer: e.target.value }))} placeholder="The correct answer" />
              </div>
              <div style={s.fieldRow3}>
                <div>
                  <label style={s.fieldLabel}>Incorrect 1 <span style={s.fieldRequired}>*</span></label>
                  <input style={s.fieldInput} value={addQForm.incorrect_1} onChange={e => setAddQForm(f => ({ ...f, incorrect_1: e.target.value }))} />
                </div>
                <div>
                  <label style={s.fieldLabel}>Incorrect 2 <span style={s.fieldRequired}>*</span></label>
                  <input style={s.fieldInput} value={addQForm.incorrect_2} onChange={e => setAddQForm(f => ({ ...f, incorrect_2: e.target.value }))} />
                </div>
                <div>
                  <label style={s.fieldLabel}>Incorrect 3 <span style={s.fieldRequired}>*</span></label>
                  <input style={s.fieldInput} value={addQForm.incorrect_3} onChange={e => setAddQForm(f => ({ ...f, incorrect_3: e.target.value }))} />
                </div>
              </div>
              <div style={s.fieldRow3}>
                <div>
                  <label style={s.fieldLabel}>Category <span style={s.fieldRequired}>*</span></label>
                  {(communityCategories || []).length > 0 ? (
                    addQForm.category === '__other__' ? (
                      <div style={{ display: 'flex', gap: '6px' }}>
                        <input style={{ ...s.fieldInput, flex: 1 }} value={addQForm._customCategory || ''} onChange={e => setAddQForm(f => ({ ...f, _customCategory: e.target.value }))} placeholder="Enter category name" />
                        <button type="button" onClick={() => setAddQForm(f => ({ ...f, category: '', _customCategory: undefined }))} style={{ padding: '4px 8px', fontSize: '12px', border: `1px solid ${t.border}`, borderRadius: '4px', background: 'transparent', cursor: 'pointer', color: t.text }}>Back</button>
                      </div>
                    ) : (
                      <select style={s.fieldSelect} value={addQForm.category} onChange={e => setAddQForm(f => ({ ...f, category: e.target.value }))}>
                        <option value="">Select category...</option>
                        {communityCategories.map(cat => <option key={cat} value={cat}>{cat}</option>)}
                        <option value="__other__">Other (custom)</option>
                      </select>
                    )
                  ) : (
                    <input style={s.fieldInput} value={addQForm.category} onChange={e => setAddQForm(f => ({ ...f, category: e.target.value }))} placeholder="e.g. Geography" />
                  )}
                </div>
                <div>
                  <label style={s.fieldLabel}>Difficulty <span style={s.fieldRequired}>*</span></label>
                  <select style={s.fieldSelect} value={addQForm.difficulty} onChange={e => setAddQForm(f => ({ ...f, difficulty: e.target.value }))}>
                    <option value="easy">Easy</option>
                    <option value="medium">Medium</option>
                    <option value="hard">Hard</option>
                  </select>
                </div>
                <div>
                  <label style={s.fieldLabel}>Tags (comma-separated)</label>
                  <input style={s.fieldInput} value={addQForm.tags} onChange={e => setAddQForm(f => ({ ...f, tags: e.target.value }))} placeholder="e.g. history, europe" />
                </div>
              </div>
              <div style={s.fieldGroup}>
                <label style={s.fieldLabel}>Explanation (optional)</label>
                <textarea style={s.fieldTextarea} rows={2} value={addQForm.explanation} onChange={e => setAddQForm(f => ({ ...f, explanation: e.target.value }))} placeholder="Why is this the correct answer? Shown after the player answers." />
              </div>
              <button style={{ ...s.submitBtn, opacity: addQSubmitting ? 0.7 : 1 }} onClick={handleAddQuestion} disabled={addQSubmitting}>
                {addQSubmitting ? 'Adding...' : 'Add Question'}
              </button>
            </div>
          </div>
        </div>
      )}

      {/* ===== IMPORT CSV MODAL ===== */}
      {activeModal === 'import' && (
        <div style={s.overlay} onClick={() => setActiveModal(null)}>
          <div style={s.modal} onClick={e => e.stopPropagation()}>
            <div style={s.modalHeader}>
              <h2 style={s.modalTitle}>Import Questions from CSV</h2>
              <button style={s.modalClose} onClick={() => setActiveModal(null)}>&times;</button>
            </div>
            <div style={s.modalBody}>
              <div style={s.uploadInstructions}>
                <p style={{ margin: '0 0 8px' }}>Upload a CSV file to bulk import questions.</p>
                <p style={{ margin: '0 0 8px', fontSize: '12px' }}>Required columns: <code>question_text</code>, <code>correct_answer</code>, <code>incorrect_answer_1</code>, <code>incorrect_answer_2</code>, <code>incorrect_answer_3</code>, <code>category</code>, <code>difficulty</code></p>
                <p style={{ margin: '0 0 12px', fontSize: '12px' }}>Optional: <code>image_url</code>, <code>video_url</code>, <code>explanation</code></p>
                <button style={s.actionBtn} onClick={downloadTemplate}><DownloadIcon size={14} /> Download CSV Template</button>
              </div>
              <div style={{ marginBottom: '16px' }}>
                <input type="file" accept=".csv" onChange={handleFileUpload} id="qm-csv-upload" style={{ display: 'none' }} />
                <label htmlFor="qm-csv-upload" style={{ ...s.actionBtn, display: 'inline-flex', cursor: 'pointer' }}><UploadIcon size={14} /> Choose CSV File</label>
              </div>

              {csvErrors.length > 0 && (
                <div style={s.csvErrorBox}>
                  <p style={{ fontWeight: 600, margin: '0 0 6px' }}>Validation Errors</p>
                  <ul style={{ margin: 0, paddingLeft: '18px' }}>{csvErrors.map((err, i) => <li key={i}>{err}</li>)}</ul>
                </div>
              )}

              {csvPreview.length > 0 && (
                <div style={{ marginTop: '16px' }}>
                  <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '8px' }}>
                    <h3 style={{ margin: 0, fontSize: '14px', fontWeight: 600, color: t.text }}>Preview ({csvPreview.length} of {csvData.length} questions)</h3>
                    {csvErrors.length === 0 && (
                      <button style={{ ...s.submitBtn, width: 'auto', padding: '8px 16px', marginTop: 0, opacity: uploading ? 0.7 : 1 }} onClick={handleBulkImport} disabled={uploading}>
                        {uploading ? 'Importing...' : `Import ${csvData.length} Questions`}
                      </button>
                    )}
                  </div>
                  <div style={{ overflowX: 'auto' }}>
                    <table style={s.previewTable}>
                      <thead>
                        <tr>
                          <th style={s.previewTh}>#</th>
                          <th style={s.previewTh}>Question</th>
                          <th style={s.previewTh}>Correct Answer</th>
                          <th style={s.previewTh}>Incorrect Answers</th>
                          <th style={s.previewTh}>Category</th>
                          <th style={s.previewTh}>Difficulty</th>
                        </tr>
                      </thead>
                      <tbody>
                        {csvPreview.map((q, i) => (
                          <tr key={i}>
                            <td style={s.previewTd}>{i + 1}</td>
                            <td style={s.previewTd}>{q.question_text}</td>
                            <td style={{ ...s.previewTd, color: t.success }}>{q.correct_answer}</td>
                            <td style={s.previewTd}>{q.incorrect_answers.join(', ')}</td>
                            <td style={s.previewTd}><span style={s.chip}>{q.category}</span></td>
                            <td style={s.previewTd}><span style={s.diffChip(q.difficulty)}>{q.difficulty}</span></td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                    {csvData.length > 5 && <p style={{ fontSize: '12px', color: t.textMuted, marginTop: '4px' }}>... and {csvData.length - 5} more questions</p>}
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default QuestionManager;
