import React, { useState, useEffect, useCallback } from 'react';
import Papa from 'papaparse';
import { supabase } from '../supabaseClient';

const THEMES = [
  { id: 'dark', label: 'Dark', bg: '#0D0D0D', surface: '#1A1A2E', primary: '#6B2D5E', accent: '#8B0000', text: '#F5F0EB' },
  { id: 'light', label: 'Light', bg: '#FFFFFF', surface: '#F5F5F7', primary: '#041E42', accent: '#4A90D9', text: '#1a1a1a' },
  { id: 'neon', label: 'Neon', bg: '#0a0a23', surface: '#1a1a3e', primary: '#00ff88', accent: '#ff6600', text: '#ffffff' },
  { id: 'warm', label: 'Warm', bg: '#FFF8F0', surface: '#FFF0E0', primary: '#8B4513', accent: '#D2691E', text: '#2C1810' },
  { id: 'ocean', label: 'Ocean', bg: '#0C1929', surface: '#132D46', primary: '#1E90FF', accent: '#00CED1', text: '#E0F0FF' },
];

const STEPS = [
  { num: 1, label: 'Create' },
  { num: 2, label: 'Questions' },
  { num: 3, label: 'Theme' },
  { num: 4, label: 'Launch' },
];

function SetupWizard({ user: initialUser }) {
  const [step, setStep] = useState(1);
  const [user, setUser] = useState(initialUser || null);
  const [community, setCommunity] = useState(null);
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  // Step 1 state
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [quizName, setQuizName] = useState('');

  // Step 2 state
  const [questionCount, setQuestionCount] = useState(0);
  const [addMode, setAddMode] = useState(null); // 'manual' | 'csv'
  const [manualForm, setManualForm] = useState({
    question_text: '', correct_answer: '',
    incorrect_1: '', incorrect_2: '', incorrect_3: '',
    category: '', difficulty: 'medium',
  });
  const [csvFile, setCsvFile] = useState(null);
  const [csvPreview, setCsvPreview] = useState(null);
  const [csvData, setCsvData] = useState(null);
  const [csvError, setCsvError] = useState('');
  const [uploading, setUploading] = useState(false);

  // Step 3 state
  const [selectedTheme, setSelectedTheme] = useState('light');

  // Listen for auth changes (e.g. after signUp)
  useEffect(() => {
    const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
      setUser(session?.user || null);
    });
    return () => subscription.unsubscribe();
  }, []);

  // Step 1: Create account + community
  const handleStep1 = async () => {
    setError('');
    if (!quizName.trim()) { setError('Quiz name is required'); return; }

    setLoading(true);
    try {
      let currentUser = user;

      // Sign up if not authenticated
      if (!currentUser) {
        if (!email.trim()) { setError('Email is required'); setLoading(false); return; }
        if (password.length < 6) { setError('Password must be at least 6 characters'); setLoading(false); return; }
        if (password !== confirmPassword) { setError('Passwords do not match'); setLoading(false); return; }

        const { data, error: signUpErr } = await supabase.auth.signUp({
          email: email.trim(),
          password,
          options: { data: { username: email.split('@')[0] } },
        });
        if (signUpErr) throw signUpErr;
        if (data?.user?.identities?.length === 0) throw new Error('An account with this email already exists.');
        currentUser = data.user;
        setUser(currentUser);
      }

      // Create community
      const slug = quizName.trim().toLowerCase().replace(/[^a-z0-9]+/g, '-').replace(/^-|-$/g, '');
      const inviteCode = Math.random().toString(36).substring(2, 10).toUpperCase();
      const { data: comm, error: commErr } = await supabase
        .from('communities')
        .insert({
          name: quizName.trim(),
          slug,
          commissioner_id: currentUser.id,
          invite_code: inviteCode,
          season_start: new Date().toISOString(),
          season_end: new Date(Date.now() + 90 * 86400000).toISOString(),
          settings: { max_members: 50 },
          visibility: 'private',
        })
        .select()
        .single();
      if (commErr) throw commErr;

      // Add as owner
      const { error: memberErr } = await supabase
        .from('community_members')
        .insert({ community_id: comm.id, user_id: currentUser.id, role: 'owner' });
      if (memberErr) throw memberErr;

      setCommunity(comm);
      setStep(2);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  // Step 2: Add manual question
  const handleAddManual = async () => {
    setError('');
    const { question_text, correct_answer, incorrect_1, incorrect_2, incorrect_3, category, difficulty } = manualForm;
    if (!question_text.trim() || !correct_answer.trim() || !incorrect_1.trim() || !incorrect_2.trim() || !incorrect_3.trim()) {
      setError('All question and answer fields are required');
      return;
    }

    setLoading(true);
    try {
      const { error: insertErr } = await supabase
        .from('community_questions')
        .insert({
          community_id: community.id,
          question_text: question_text.trim(),
          correct_answer: correct_answer.trim(),
          incorrect_answers: [incorrect_1.trim(), incorrect_2.trim(), incorrect_3.trim()],
          category: category.trim() || 'General',
          difficulty,
          source: 'manual',
          status: 'active',
        });
      if (insertErr) throw insertErr;
      setQuestionCount(c => c + 1);
      setManualForm({
        question_text: '', correct_answer: '',
        incorrect_1: '', incorrect_2: '', incorrect_3: '',
        category: '', difficulty: 'medium',
      });
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  // Step 2: CSV handling
  const handleCsvSelect = useCallback((e) => {
    const file = e.target.files?.[0];
    if (!file) return;
    setCsvFile(file);
    setCsvError('');
    setCsvPreview(null);
    setCsvData(null);

    Papa.parse(file, {
      header: true,
      skipEmptyLines: true,
      complete: (results) => {
        const required = ['question_text', 'correct_answer', 'incorrect_answer_1', 'incorrect_answer_2', 'incorrect_answer_3'];
        const headers = results.meta.fields || [];
        const missing = required.filter(r => !headers.includes(r));
        if (missing.length > 0) {
          setCsvError(`Missing required columns: ${missing.join(', ')}`);
          return;
        }
        const valid = results.data.filter(row => row.question_text?.trim() && row.correct_answer?.trim());
        if (valid.length === 0) {
          setCsvError('No valid rows found');
          return;
        }
        setCsvPreview(valid.slice(0, 5));
        setCsvData(valid);
      },
      error: (err) => setCsvError('Failed to parse CSV: ' + err.message),
    });
  }, []);

  const handleCsvImport = async () => {
    if (!csvData?.length) return;
    setUploading(true);
    setError('');
    try {
      const rows = csvData.map(row => ({
        community_id: community.id,
        question_text: row.question_text.trim(),
        correct_answer: row.correct_answer.trim(),
        incorrect_answers: [
          (row.incorrect_answer_1 || '').trim(),
          (row.incorrect_answer_2 || '').trim(),
          (row.incorrect_answer_3 || '').trim(),
        ],
        category: (row.category || 'General').trim(),
        difficulty: ['easy', 'medium', 'hard'].includes(row.difficulty) ? row.difficulty : 'medium',
        source: 'imported',
        status: 'active',
      }));
      const { error: insertErr } = await supabase.from('community_questions').insert(rows);
      if (insertErr) throw insertErr;
      setQuestionCount(c => c + rows.length);
      setCsvFile(null);
      setCsvPreview(null);
      setCsvData(null);
      setAddMode(null);
    } catch (err) {
      setError(err.message);
    } finally {
      setUploading(false);
    }
  };

  // Step 3: Save theme
  const handleSaveTheme = async () => {
    setLoading(true);
    try {
      const theme = THEMES.find(t => t.id === selectedTheme) || THEMES[1];
      const newSettings = {
        ...(community.settings || {}),
        embed_theme: {
          id: theme.id,
          bg: theme.bg,
          surface: theme.surface,
          primary: theme.primary,
          accent: theme.accent,
          text: theme.text,
        },
      };
      const { error: updateErr } = await supabase
        .from('communities')
        .update({ settings: newSettings })
        .eq('id', community.id);
      if (updateErr) throw updateErr;
      setCommunity(prev => ({ ...prev, settings: newSettings }));
      setStep(4);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  // Build embed URL with theme params
  const getEmbedUrl = () => {
    const theme = community?.settings?.embed_theme || THEMES[1];
    const base = `https://gstreet-ops.github.io/gstreet-ops-quiz-embed/?community=${community?.slug}`;
    const params = `&bg=${encodeURIComponent(theme.bg)}&surface=${encodeURIComponent(theme.surface)}&primary=${encodeURIComponent(theme.primary)}&accent=${encodeURIComponent(theme.accent)}&text=${encodeURIComponent(theme.text)}`;
    return base + params;
  };

  const copyToClipboard = (text) => {
    navigator.clipboard.writeText(text).catch(() => {});
  };

  return (
    <div style={{
      minHeight: '100vh',
      backgroundColor: '#F5F5F7',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      fontFamily: 'system-ui, -apple-system, sans-serif',
      padding: '2rem 1rem',
    }}>
      <div style={{ width: '100%', maxWidth: '640px' }}>
        {/* Step indicator */}
        <div style={{ display: 'flex', justifyContent: 'center', gap: '8px', marginBottom: '2rem' }}>
          {STEPS.map((s, i) => (
            <div key={s.num} style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
              <div style={{
                width: '32px',
                height: '32px',
                borderRadius: '50%',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '0.8125rem',
                fontWeight: 600,
                backgroundColor: step >= s.num ? '#041E42' : '#E5E7EB',
                color: step >= s.num ? '#FFFFFF' : '#9CA3AF',
                transition: 'all 0.2s ease',
              }}>
                {step > s.num ? '' : s.num}
              </div>
              <span style={{
                fontSize: '0.75rem',
                color: step >= s.num ? '#041E42' : '#9CA3AF',
                fontWeight: step === s.num ? 600 : 400,
                display: i < STEPS.length - 1 ? 'inline' : 'inline',
              }}>
                {s.label}
              </span>
              {i < STEPS.length - 1 && (
                <div style={{
                  width: '24px',
                  height: '2px',
                  backgroundColor: step > s.num ? '#041E42' : '#E5E7EB',
                }} />
              )}
            </div>
          ))}
        </div>

        {/* Card */}
        <div style={{
          backgroundColor: '#FFFFFF',
          borderRadius: '12px',
          padding: '2.5rem',
          boxShadow: '0 4px 24px rgba(0, 0, 0, 0.08)',
        }}>
          {/* Error */}
          {error && (
            <div style={{
              padding: '10px 14px',
              borderRadius: '6px',
              backgroundColor: '#FEE2E2',
              color: '#991B1B',
              fontSize: '0.8125rem',
              marginBottom: '1.5rem',
            }}>
              {error}
            </div>
          )}

          {step === 1 && (
            <Step1
              user={user}
              email={email} setEmail={setEmail}
              password={password} setPassword={setPassword}
              confirmPassword={confirmPassword} setConfirmPassword={setConfirmPassword}
              quizName={quizName} setQuizName={setQuizName}
              loading={loading}
              onSubmit={handleStep1}
            />
          )}

          {step === 2 && (
            <Step2
              addMode={addMode} setAddMode={setAddMode}
              manualForm={manualForm} setManualForm={setManualForm}
              questionCount={questionCount}
              loading={loading}
              onAddManual={handleAddManual}
              onCsvSelect={handleCsvSelect}
              csvFile={csvFile} csvPreview={csvPreview} csvData={csvData}
              csvError={csvError} uploading={uploading}
              onCsvImport={handleCsvImport}
              onContinue={() => setStep(3)}
              onBack={() => setStep(1)}
              onSkip={() => setStep(3)}
              setError={setError}
            />
          )}

          {step === 3 && (
            <Step3
              selectedTheme={selectedTheme}
              setSelectedTheme={setSelectedTheme}
              loading={loading}
              onContinue={handleSaveTheme}
              onBack={() => setStep(2)}
            />
          )}

          {step === 4 && (
            <Step4
              community={community}
              getEmbedUrl={getEmbedUrl}
              copyToClipboard={copyToClipboard}
            />
          )}
        </div>
      </div>
    </div>
  );
}

/*  STEP 1: Create Account & Name  */
function Step1({ user, email, setEmail, password, setPassword, confirmPassword, setConfirmPassword, quizName, setQuizName, loading, onSubmit }) {
  return (
    <div>
      <h2 style={headingStyle}>Create Your Quiz</h2>
      <p style={descStyle}>
        {user ? 'Name your quiz to get started.' : 'Create an account and name your quiz to get started.'}
      </p>

      {!user && (
        <>
          <FieldGroup label="Email">
            <input type="email" value={email} onChange={e => setEmail(e.target.value)} style={inputStyle} placeholder="you@example.com" />
          </FieldGroup>
          <FieldGroup label="Password">
            <input type="password" value={password} onChange={e => setPassword(e.target.value)} style={inputStyle} placeholder="At least 6 characters" />
          </FieldGroup>
          <FieldGroup label="Confirm Password">
            <input type="password" value={confirmPassword} onChange={e => setConfirmPassword(e.target.value)} style={inputStyle} />
          </FieldGroup>
        </>
      )}

      <FieldGroup label="Quiz Name">
        <input type="text" value={quizName} onChange={e => setQuizName(e.target.value)} style={inputStyle} placeholder="e.g. Star Wars Trivia" />
      </FieldGroup>

      <button onClick={onSubmit} disabled={loading} style={primaryBtnStyle}>
        {loading ? 'Creating...' : 'Continue'}
      </button>
    </div>
  );
}

/*  STEP 2: Add Questions  */
function Step2({ addMode, setAddMode, manualForm, setManualForm, questionCount, loading, onAddManual, onCsvSelect, csvFile, csvPreview, csvData, csvError, uploading, onCsvImport, onContinue, onBack, onSkip, setError }) {
  return (
    <div>
      <h2 style={headingStyle}>Add Questions</h2>
      <p style={descStyle}>Add at least one question to your quiz, or skip and add them later.</p>

      {questionCount > 0 && (
        <div style={{
          padding: '10px 14px',
          borderRadius: '6px',
          backgroundColor: '#D1FAE5',
          color: '#065F46',
          fontSize: '0.875rem',
          fontWeight: 500,
          marginBottom: '1.5rem',
        }}>
          {questionCount} question{questionCount !== 1 ? 's' : ''} added
        </div>
      )}

      {!addMode && (
        <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '12px', marginBottom: '1.5rem' }}>
          <OptionCard
            icon=""
            title="Add Manually"
            desc="Type in questions one at a time"
            onClick={() => { setAddMode('manual'); setError(''); }}
          />
          <OptionCard
            icon=""
            title="Import CSV"
            desc="Upload a spreadsheet of questions"
            onClick={() => { setAddMode('csv'); setError(''); }}
          />
        </div>
      )}

      {addMode === 'manual' && (
        <div>
          <FieldGroup label="Question">
            <textarea
              value={manualForm.question_text}
              onChange={e => setManualForm(f => ({ ...f, question_text: e.target.value }))}
              rows={2}
              style={{ ...inputStyle, resize: 'vertical' }}
              placeholder="What is the capital of France?"
            />
          </FieldGroup>
          <FieldGroup label="Correct Answer">
            <input type="text" value={manualForm.correct_answer} onChange={e => setManualForm(f => ({ ...f, correct_answer: e.target.value }))} style={inputStyle} placeholder="Paris" />
          </FieldGroup>
          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr 1fr', gap: '8px' }}>
            <FieldGroup label="Wrong Answer 1">
              <input type="text" value={manualForm.incorrect_1} onChange={e => setManualForm(f => ({ ...f, incorrect_1: e.target.value }))} style={inputStyle} placeholder="London" />
            </FieldGroup>
            <FieldGroup label="Wrong Answer 2">
              <input type="text" value={manualForm.incorrect_2} onChange={e => setManualForm(f => ({ ...f, incorrect_2: e.target.value }))} style={inputStyle} placeholder="Berlin" />
            </FieldGroup>
            <FieldGroup label="Wrong Answer 3">
              <input type="text" value={manualForm.incorrect_3} onChange={e => setManualForm(f => ({ ...f, incorrect_3: e.target.value }))} style={inputStyle} placeholder="Madrid" />
            </FieldGroup>
          </div>
          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '8px' }}>
            <FieldGroup label="Category">
              <input type="text" value={manualForm.category} onChange={e => setManualForm(f => ({ ...f, category: e.target.value }))} style={inputStyle} placeholder="Geography" />
            </FieldGroup>
            <FieldGroup label="Difficulty">
              <select value={manualForm.difficulty} onChange={e => setManualForm(f => ({ ...f, difficulty: e.target.value }))} style={inputStyle}>
                <option value="easy">Easy</option>
                <option value="medium">Medium</option>
                <option value="hard">Hard</option>
              </select>
            </FieldGroup>
          </div>
          <div style={{ display: 'flex', gap: '8px', marginBottom: '1rem' }}>
            <button onClick={onAddManual} disabled={loading} style={primaryBtnStyle}>
              {loading ? 'Adding...' : 'Add Question'}
            </button>
            <button onClick={() => setAddMode(null)} style={secondaryBtnStyle}>
              Back
            </button>
          </div>
        </div>
      )}

      {addMode === 'csv' && (
        <div>
          <div style={{
            border: '2px dashed #D1D5DB',
            borderRadius: '8px',
            padding: '2rem',
            textAlign: 'center',
            marginBottom: '1rem',
          }}>
            <input
              type="file"
              accept=".csv"
              onChange={onCsvSelect}
              style={{ display: 'none' }}
              id="csv-upload"
            />
            <label htmlFor="csv-upload" style={{
              cursor: 'pointer',
              color: '#4A90D9',
              fontWeight: 500,
              fontSize: '0.875rem',
            }}>
              {csvFile ? csvFile.name : 'Click to select a CSV file'}
            </label>
            <p style={{ fontSize: '0.75rem', color: '#9CA3AF', marginTop: '8px' }}>
              Required columns: question_text, correct_answer, incorrect_answer_1, incorrect_answer_2, incorrect_answer_3
            </p>
          </div>

          {csvError && (
            <div style={{ padding: '10px', borderRadius: '6px', backgroundColor: '#FEE2E2', color: '#991B1B', fontSize: '0.8125rem', marginBottom: '1rem' }}>
              {csvError}
            </div>
          )}

          {csvPreview && (
            <div style={{ marginBottom: '1rem' }}>
              <div style={{ fontSize: '0.8125rem', fontWeight: 500, marginBottom: '8px', color: '#041E42' }}>
                Preview ({csvData.length} rows total)
              </div>
              {csvPreview.map((row, i) => (
                <div key={i} style={{
                  padding: '8px 12px',
                  backgroundColor: i % 2 === 0 ? '#F9FAFB' : '#FFFFFF',
                  borderRadius: i === 0 ? '6px 6px 0 0' : i === csvPreview.length - 1 ? '0 0 6px 6px' : '0',
                  fontSize: '0.8125rem',
                  color: '#374151',
                  borderBottom: '1px solid #F3F4F6',
                }}>
                  <strong>Q:</strong> {row.question_text?.substring(0, 80)}{row.question_text?.length > 80 ? '...' : ''}
                </div>
              ))}
              <div style={{ display: 'flex', gap: '8px', marginTop: '12px' }}>
                <button onClick={onCsvImport} disabled={uploading} style={primaryBtnStyle}>
                  {uploading ? 'Importing...' : `Import ${csvData.length} Questions`}
                </button>
                <button onClick={() => setAddMode(null)} style={secondaryBtnStyle}>
                  Cancel
                </button>
              </div>
            </div>
          )}

          {!csvPreview && !csvError && (
            <button onClick={() => setAddMode(null)} style={secondaryBtnStyle}>
              Back
            </button>
          )}
        </div>
      )}

      {/* Navigation */}
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '1.5rem', borderTop: '1px solid #F3F4F6', paddingTop: '1.5rem' }}>
        <button onClick={onBack} style={secondaryBtnStyle}>Back</button>
        <div style={{ display: 'flex', alignItems: 'center', gap: '16px' }}>
          <button onClick={onSkip} style={linkBtnStyle}>Skip for now</button>
          <button onClick={onContinue} disabled={questionCount === 0} style={{
            ...primaryBtnStyle,
            opacity: questionCount === 0 ? 0.5 : 1,
            cursor: questionCount === 0 ? 'not-allowed' : 'pointer',
          }}>
            Continue
          </button>
        </div>
      </div>
    </div>
  );
}

/*  STEP 3: Pick a Theme  */
function Step3({ selectedTheme, setSelectedTheme, loading, onContinue, onBack }) {
  return (
    <div>
      <h2 style={headingStyle}>Pick a Theme</h2>
      <p style={descStyle}>Choose a look for your embedded quiz.</p>

      <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(100px, 1fr))', gap: '12px', marginBottom: '2rem' }}>
        {THEMES.map(theme => (
          <button
            key={theme.id}
            onClick={() => setSelectedTheme(theme.id)}
            style={{
              padding: '0',
              border: selectedTheme === theme.id ? '2px solid #4A90D9' : '2px solid #E5E7EB',
              borderRadius: '8px',
              cursor: 'pointer',
              overflow: 'hidden',
              background: 'none',
              transition: 'border-color 0.15s ease',
            }}
          >
            {/* Mini preview */}
            <div style={{
              backgroundColor: theme.bg,
              padding: '12px 8px',
              display: 'flex',
              flexDirection: 'column',
              gap: '4px',
              alignItems: 'center',
            }}>
              <div style={{
                width: '100%',
                height: '6px',
                borderRadius: '3px',
                backgroundColor: theme.primary,
              }} />
              <div style={{
                width: '70%',
                height: '6px',
                borderRadius: '3px',
                backgroundColor: theme.accent,
              }} />
              <div style={{
                width: '85%',
                height: '4px',
                borderRadius: '2px',
                backgroundColor: theme.text,
                opacity: 0.3,
                marginTop: '2px',
              }} />
              <div style={{
                width: '85%',
                height: '4px',
                borderRadius: '2px',
                backgroundColor: theme.text,
                opacity: 0.2,
              }} />
            </div>
            <div style={{
              padding: '8px',
              backgroundColor: selectedTheme === theme.id ? '#EFF6FF' : '#FFFFFF',
              fontSize: '0.75rem',
              fontWeight: 600,
              color: '#041E42',
              textAlign: 'center',
            }}>
              {theme.label}
            </div>
          </button>
        ))}
      </div>

      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <button onClick={onBack} style={secondaryBtnStyle}>Back</button>
        <button onClick={onContinue} disabled={loading} style={primaryBtnStyle}>
          {loading ? 'Saving...' : 'Continue'}
        </button>
      </div>
    </div>
  );
}

/*  STEP 4: Launch  */
function Step4({ community, getEmbedUrl, copyToClipboard }) {
  const embedUrl = getEmbedUrl();
  const iframeSnippet = `<iframe src="${embedUrl}" width="100%" height="600" frameborder="0"></iframe>`;
  const wcSnippet = `<script src="https://gstreet-ops.github.io/gstreet-ops-quiz-embed/quiz-embed.js"></script>\n<quiz-embed community="${community.slug}"></quiz-embed>`;
  const [copiedField, setCopiedField] = useState('');

  const handleCopy = (text, field) => {
    copyToClipboard(text);
    setCopiedField(field);
    setTimeout(() => setCopiedField(''), 2000);
  };

  return (
    <div style={{ textAlign: 'center' }}>
      <div style={{ fontSize: '3rem', marginBottom: '0.5rem' }}></div>
      <h2 style={{ ...headingStyle, textAlign: 'center' }}>Your quiz is live!</h2>
      <p style={{ ...descStyle, textAlign: 'center' }}>
        <strong>{community.name}</strong> is ready to embed on your site.
      </p>

      <div style={{ textAlign: 'left', marginTop: '2rem' }}>
        <FieldGroup label="Community Slug">
          <div style={{ display: 'flex', gap: '8px', alignItems: 'center' }}>
            <code style={codeDisplayStyle}>{community.slug}</code>
            <CopyBtn copied={copiedField === 'slug'} onClick={() => handleCopy(community.slug, 'slug')} />
          </div>
        </FieldGroup>

        <FieldGroup label="iFrame Embed">
          <div style={{ position: 'relative' }}>
            <pre style={preStyle}>{iframeSnippet}</pre>
            <CopyBtn
              copied={copiedField === 'iframe'}
              onClick={() => handleCopy(iframeSnippet, 'iframe')}
              style={{ position: 'absolute', top: '8px', right: '8px' }}
            />
          </div>
        </FieldGroup>

        <FieldGroup label="Web Component">
          <div style={{ position: 'relative' }}>
            <pre style={preStyle}>{wcSnippet}</pre>
            <CopyBtn
              copied={copiedField === 'wc'}
              onClick={() => handleCopy(wcSnippet, 'wc')}
              style={{ position: 'absolute', top: '8px', right: '8px' }}
            />
          </div>
        </FieldGroup>
      </div>

      <div style={{ display: 'flex', flexDirection: 'column', gap: '12px', marginTop: '2rem' }}>
        <a
          href={embedUrl}
          target="_blank"
          rel="noopener noreferrer"
          style={{
            ...primaryBtnStyle,
            textDecoration: 'none',
            textAlign: 'center',
            display: 'block',
            backgroundColor: '#059669',
          }}
        >
          Test Your Embed 
        </a>
        <button
          onClick={() => {
            window.location.hash = `#/admin?community=${community.slug}`;
          }}
          style={primaryBtnStyle}
        >
          Go to Admin Panel
        </button>
      </div>
    </div>
  );
}

/*  Shared sub-components  */
function FieldGroup({ label, children }) {
  return (
    <div style={{ marginBottom: '16px' }}>
      <label style={{
        display: 'block',
        fontSize: '0.8125rem',
        fontWeight: 500,
        color: '#374151',
        marginBottom: '6px',
      }}>
        {label}
      </label>
      {children}
    </div>
  );
}

function OptionCard({ icon, title, desc, onClick }) {
  return (
    <button
      onClick={onClick}
      style={{
        padding: '20px 16px',
        background: '#FFFFFF',
        border: '1px solid #E5E7EB',
        borderRadius: '8px',
        cursor: 'pointer',
        textAlign: 'center',
        fontFamily: 'inherit',
        transition: 'border-color 0.15s ease',
      }}
      onMouseEnter={e => { e.currentTarget.style.borderColor = '#4A90D9'; }}
      onMouseLeave={e => { e.currentTarget.style.borderColor = '#E5E7EB'; }}
    >
      <div style={{ fontSize: '1.5rem', marginBottom: '8px' }}>{icon}</div>
      <div style={{ fontSize: '0.875rem', fontWeight: 600, color: '#041E42', marginBottom: '4px' }}>{title}</div>
      <div style={{ fontSize: '0.75rem', color: '#54585A' }}>{desc}</div>
    </button>
  );
}

function CopyBtn({ onClick, copied, style }) {
  return (
    <button
      onClick={onClick}
      style={{
        background: '#F5F5F7',
        border: '1px solid #E5E7EB',
        borderRadius: '4px',
        padding: '4px 10px',
        fontSize: '0.75rem',
        cursor: 'pointer',
        fontFamily: 'inherit',
        color: copied ? '#059669' : '#374151',
        whiteSpace: 'nowrap',
        minWidth: '52px',
        ...style,
      }}
    >
      {copied ? 'Copied!' : 'Copy'}
    </button>
  );
}

