import React, { useState, useEffect, useCallback } from 'react';
import { supabase } from '../../supabaseClient';

function DashboardTab({ community, communityRole, onToast, onNavigate }) {
  const [stats, setStats] = useState({ questions: 0, games: 0, players: 0, members: 0 });
  const [recentGames, setRecentGames] = useState([]);
  const [loading, setLoading] = useState(true);

  const fetchDashboardData = useCallback(async () => {
    setLoading(true);
    try {
      const [questionsRes, gamesRes, membersRes, recentRes] = await Promise.all([
        supabase
          .from('community_questions')
          .select('id', { count: 'exact', head: true })
          .eq('community_id', community.id)
          .eq('status', 'active'),
        supabase
          .from('games')
          .select('user_id')
          .eq('community_id', community.id),
        supabase
          .from('community_members')
          .select('id', { count: 'exact', head: true })
          .eq('community_id', community.id),
        supabase
          .from('games')
          .select('id, score, total_questions, category, created_at, source, profiles(username)')
          .eq('community_id', community.id)
          .order('created_at', { ascending: false })
          .limit(5),
      ]);

      const uniquePlayers = new Set((gamesRes.data || []).map(g => g.user_id));

      setStats({
        questions: questionsRes.count || 0,
        games: (gamesRes.data || []).length,
        players: uniquePlayers.size,
        members: membersRes.count || 0,
      });
      setRecentGames(recentRes.data || []);
    } catch (err) {
      onToast?.('Failed to load dashboard data', 'error');
    } finally {
      setLoading(false);
    }
  }, [community.id, onToast]);

  useEffect(() => {
    fetchDashboardData();
  }, [fetchDashboardData]);

  const statCards = [
    { label: 'Questions', value: stats.questions, icon: '', color: '#4A90D9' },
    { label: 'Games Played', value: stats.games, icon: '', color: '#059669' },
    { label: 'Active Players', value: stats.players, icon: '', color: '#7C3AED' },
    { label: 'Members', value: stats.members, icon: '', color: '#D97706' },
  ];

  const formatDate = (dateStr) => {
    const d = new Date(dateStr);
    const now = new Date();
    const diffMs = now - d;
    const diffMins = Math.floor(diffMs / 60000);
    if (diffMins < 1) return 'just now';
    if (diffMins < 60) return `${diffMins}m ago`;
    const diffHours = Math.floor(diffMins / 60);
    if (diffHours < 24) return `${diffHours}h ago`;
    const diffDays = Math.floor(diffHours / 24);
    if (diffDays < 7) return `${diffDays}d ago`;
    return d.toLocaleDateString();
  };

  return (
    <div>
      <h1 style={{ color: '#041E42', fontSize: '1.5rem', marginBottom: '0.5rem', fontWeight: 600 }}>
        Dashboard
      </h1>
      <p style={{ color: '#54585A', marginBottom: '1.5rem' }}>
        Overview for {community.name}
      </p>

      {/* Stat Cards */}
      <div style={{
        display: 'grid',
        gridTemplateColumns: 'repeat(auto-fit, minmax(180px, 1fr))',
        gap: '16px',
        marginBottom: '2rem',
      }}>
        {statCards.map(card => (
          <div key={card.label} style={{
            background: '#FFFFFF',
            borderRadius: '8px',
            padding: '20px',
            border: '1px solid #E5E7EB',
          }}>
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
              <div>
                <div style={{ fontSize: '0.8125rem', color: '#54585A', marginBottom: '4px' }}>
                  {card.label}
                </div>
                <div style={{ fontSize: '1.75rem', fontWeight: 700, color: '#041E42' }}>
                  {loading ? '' : card.value.toLocaleString()}
                </div>
              </div>
              <div style={{
                width: '40px',
                height: '40px',
                borderRadius: '8px',
                backgroundColor: card.color + '15',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '1.25rem',
              }}>
                {card.icon}
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Recent Activity */}
      <div style={{
        background: '#FFFFFF',
        borderRadius: '8px',
        border: '1px solid #E5E7EB',
        marginBottom: '2rem',
      }}>
        <div style={{
          padding: '16px 20px',
          borderBottom: '1px solid #E5E7EB',
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
        }}>
          <h2 style={{ margin: 0, fontSize: '1rem', fontWeight: 600, color: '#041E42' }}>
            Recent Activity
          </h2>
          {onNavigate && (
            <button
              onClick={() => onNavigate('analytics')}
              style={{
                background: 'none',
                border: 'none',
                color: '#4A90D9',
                fontSize: '0.8125rem',
                cursor: 'pointer',
                fontFamily: 'inherit',
              }}
            >
              View all 
            </button>
          )}
        </div>

        {loading ? (
          <div style={{ padding: '2rem', textAlign: 'center', color: '#54585A' }}>
            Loading...
          </div>
        ) : recentGames.length === 0 ? (
          <div style={{ padding: '2rem', textAlign: 'center', color: '#54585A' }}>
            No games played yet. Share your community invite code to get started!
          </div>
        ) : (
          <div>
            {recentGames.map((game, i) => (
              <div key={game.id} style={{
                padding: '12px 20px',
                borderBottom: i < recentGames.length - 1 ? '1px solid #F3F4F6' : 'none',
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
              }}>
                <div style={{ display: 'flex', alignItems: 'center', gap: '12px' }}>
                  <div style={{
                    width: '32px',
                    height: '32px',
                    borderRadius: '50%',
                    backgroundColor: '#E8ECF0',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    fontSize: '0.75rem',
                    fontWeight: 600,
                    color: '#041E42',
                  }}>
                    {(game.profiles?.username || '?')[0].toUpperCase()}
                  </div>
                  <div>
                    <div style={{ fontSize: '0.875rem', fontWeight: 500, color: '#1a1a1a' }}>
                      {game.profiles?.username || 'Anonymous'}
                    </div>
                    <div style={{ fontSize: '0.75rem', color: '#54585A' }}>
                      {game.category || 'Mixed'}  {game.source === 'embed' ? 'Embed' : 'App'}
                    </div>
                  </div>
                </div>
                <div style={{ textAlign: 'right' }}>
                  <div style={{
                    fontSize: '0.875rem',
                    fontWeight: 600,
                    color: game.score === game.total_questions ? '#059669' : '#041E42',
                  }}>
                    {game.score}/{game.total_questions}
                  </div>
                  <div style={{ fontSize: '0.6875rem', color: '#9CA3AF' }}>
                    {formatDate(game.created_at)}
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Quick Actions */}
      <div style={{
        background: '#FFFFFF',
        borderRadius: '8px',
        border: '1px solid #E5E7EB',
      }}>
        <div style={{
          padding: '16px 20px',
          borderBottom: '1px solid #E5E7EB',
        }}>
          <h2 style={{ margin: 0, fontSize: '1rem', fontWeight: 600, color: '#041E42' }}>
            Quick Actions
          </h2>
        </div>
        <div style={{ padding: '16px 20px', display: 'flex', flexWrap: 'wrap', gap: '10px' }}>
          {onNavigate && (
            <>
              <ActionButton
                label="Manage Questions"
                icon=""
                onClick={() => onNavigate('questions')}
              />
              <ActionButton
                label="View Analytics"
                icon=""
                onClick={() => onNavigate('analytics')}
              />
              <ActionButton
                label="Community Settings"
                icon=""
                onClick={() => onNavigate('settings')}
              />
            </>
          )}
          <ActionButton
            label="Copy Invite Code"
            icon=""
            onClick={() => {
              navigator.clipboard.writeText(community.invite_code || '').then(() => {
                onToast?.('Invite code copied!');
              }).catch(() => {
                onToast?.('Failed to copy', 'error');
              });
            }}
          />
        </div>
      </div>
    </div>
  );
}

function ActionButton({ label, icon, onClick }) {
  return (
    <button
      onClick={onClick}
      style={{
        display: 'flex',
        alignItems: 'center',
        gap: '8px',
        padding: '10px 16px',
        background: '#F5F5F7',
        border: '1px solid #E5E7EB',
        borderRadius: '6px',
        fontSize: '0.8125rem',
        fontWeight: 500,
        color: '#041E42',
        cursor: 'pointer',
        fontFamily: 'inherit',
        transition: 'background 0.15s ease',
      }}
      onMouseEnter={e => { e.currentTarget.style.background = '#E8ECF0'; }}
      onMouseLeave={e => { e.currentTarget.style.background = '#F5F5F7'; }}
    >
      <span>{icon}</span>
      <span>{label}</span>
    </button>
  );
}

export default DashboardTab;
