import React, { useState, useEffect } from 'react';
import { useAdminAuth } from './useAdminAuth';
import { hasCommunityRole } from './permissions';
import AdminAuth from './AdminAuth';
import AdminShell from './AdminShell';
import SetupWizard from './SetupWizard';
import { supabase } from '../supabaseClient';

/**
 * Parse community slug from URL.
 * Supports both:
 *   /#/admin?community=SLUG  (query params in hash)
 *   ?community=SLUG#/admin   (query params before hash)
 * Hash-based params take priority.
 */
function getCommunitySlug() {
  // Try hash-based query params first: /#/admin?community=slug
  const hash = window.location.hash;
  const hashQueryIndex = hash.indexOf('?');
  if (hashQueryIndex !== -1) {
    const hashParams = new URLSearchParams(hash.substring(hashQueryIndex));
    const slug = hashParams.get('community');
    if (slug) return slug;
  }

  // Fallback to regular query params: ?community=slug#/admin
  const searchParams = new URLSearchParams(window.location.search);
  return searchParams.get('community');
}

/**
 * Check if the current hash is the setup wizard route.
 */
function isSetupRoute() {
  return window.location.hash.startsWith('#/admin/setup');
}

/**
 * AdminApp  Entry point for /#/admin routes.
 * Handles auth gating and role verification before rendering the admin shell.
 */
function AdminApp() {
  const [onSetupRoute, setOnSetupRoute] = useState(isSetupRoute());
  const communitySlug = getCommunitySlug();
  const { user, community, communityRole, loading, error, login, register, logout } = useAdminAuth(communitySlug);

  // Track hash changes for setup route detection
  useEffect(() => {
    const onHashChange = () => setOnSetupRoute(isSetupRoute());
    window.addEventListener('hashchange', onHashChange);
    return () => window.removeEventListener('hashchange', onHashChange);
  }, []);

  // Setup wizard route
  if (onSetupRoute) {
    return <SetupWizardGate user={user} loading={loading} />;
  }

  // No community specified  check if user has any communities
  if (!communitySlug) {
    return <NoCommunityGate user={user} loading={loading} logout={logout} />;
  }

  // Loading state
  if (loading) {
    return <LoadingScreen />;
  }

  // Not authenticated  show login
  if (!user) {
    return (
      <AdminAuth
        communityName={community?.name || communitySlug}
        onLogin={login}
        onRegister={register}
      />
    );
  }

  // Auth error
  if (error) {
    return (
      <div style={centerStyle}>
        <div style={cardStyle}>
          <div style={{ fontSize: '2rem', marginBottom: '1rem' }}></div>
          <h2 style={{ color: '#041E42', fontSize: '1.25rem', marginBottom: '0.5rem' }}>Error</h2>
          <p style={{ color: '#54585A', fontSize: '0.875rem' }}>{error}</p>
          <button onClick={logout} style={linkButtonStyle}>Sign out and try again</button>
        </div>
      </div>
    );
  }

  // Authenticated but no community role or insufficient access
  if (!communityRole || !hasCommunityRole(communityRole, 'moderator')) {
    return (
      <div style={centerStyle}>
        <div style={cardStyle}>
          <div style={{ fontSize: '2rem', marginBottom: '1rem' }}></div>
          <h2 style={{ color: '#041E42', fontSize: '1.25rem', marginBottom: '0.5rem' }}>Access Denied</h2>
          <p style={{ color: '#54585A', fontSize: '0.875rem', marginBottom: '1rem' }}>
            You don't have permission to manage <strong>{community?.name || communitySlug}</strong>.
            <br />Moderator access or higher is required.
          </p>
          <button onClick={logout} style={linkButtonStyle}>Sign out</button>
        </div>
      </div>
    );
  }

  // Authenticated + has role  show admin shell
  return (
    <AdminShell
      user={user}
      community={community}
      communityRole={communityRole}
      onLogout={logout}
    />
  );
}

/**
 * SetupWizardGate  Checks auth session before showing setup wizard.
 * The wizard handles its own signup flow, so unauthenticated users are allowed.
 */
function SetupWizardGate({ user, loading }) {
  if (loading) return <LoadingScreen />;
  return <SetupWizard user={user} />;
}

/**
 * NoCommunityGate  When no ?community= param, check if user has communities.
 * If authenticated with no communities  redirect to setup.
 * If authenticated with communities  show them.
 * If not authenticated  show the "no community specified" message.
 */
function NoCommunityGate({ user, loading, logout }) {
  const [userCommunities, setUserCommunities] = useState(null);
  const [checking, setChecking] = useState(false);

  useEffect(() => {
    if (!user) return;
    setChecking(true);
    supabase
      .from('community_members')
      .select('community_id, role, communities(id, name, slug)')
      .eq('user_id', user.id)
      .then(({ data }) => {
        const communities = (data || [])
          .filter(m => m.communities)
          .map(m => ({ ...m.communities, role: m.role }));
        setUserCommunities(communities);

        // No communities  redirect to setup
        if (communities.length === 0) {
          window.location.hash = '#/admin/setup';
        }
        setChecking(false);
      });
  }, [user]);

  if (loading || checking) return <LoadingScreen />;

  // Not authenticated
  if (!user) {
    return (
      <div style={centerStyle}>
        <div style={cardStyle}>
          <div style={{ fontSize: '2rem', marginBottom: '1rem' }}></div>
          <h2 style={{ color: '#041E42', fontSize: '1.25rem', marginBottom: '0.5rem' }}>No Community Specified</h2>
          <p style={{ color: '#54585A', fontSize: '0.875rem', marginBottom: '1.5rem' }}>
            Add <code style={codeStyle}>?community=your-slug</code> to the URL to access the admin panel.
          </p>
          <p style={{ color: '#54585A', fontSize: '0.875rem' }}>
            Or{' '}
            <a
              href="#/admin/setup"
              style={{ color: '#4A90D9', fontWeight: 500 }}
            >
              create a new quiz
            </a>
          </p>
        </div>
      </div>
    );
  }

  // Authenticated with communities  show list
  if (userCommunities && userCommunities.length > 0) {
    return (
      <div style={centerStyle}>
        <div style={{ ...cardStyle, maxWidth: '480px', textAlign: 'left' }}>
          <h2 style={{ color: '#041E42', fontSize: '1.25rem', marginBottom: '0.25rem', textAlign: 'center' }}>
            Your Communities
          </h2>
          <p style={{ color: '#54585A', fontSize: '0.8125rem', marginBottom: '1.5rem', textAlign: 'center' }}>
            Select a community to manage
          </p>
          {userCommunities.map(c => (
            <a
              key={c.id}
              href={`#/admin?community=${c.slug}`}
              style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                padding: '12px 16px',
                borderRadius: '8px',
                border: '1px solid #E5E7EB',
                marginBottom: '8px',
                textDecoration: 'none',
                color: 'inherit',
                transition: 'border-color 0.15s ease',
              }}
              onMouseEnter={e => { e.currentTarget.style.borderColor = '#4A90D9'; }}
              onMouseLeave={e => { e.currentTarget.style.borderColor = '#E5E7EB'; }}
            >
              <div>
                <div style={{ fontWeight: 600, color: '#041E42', fontSize: '0.9375rem' }}>{c.name}</div>
                <div style={{ fontSize: '0.75rem', color: '#9CA3AF', textTransform: 'capitalize' }}>{c.role}</div>
              </div>
              <span style={{ color: '#9CA3AF', fontSize: '1.25rem' }}></span>
            </a>
          ))}
          <div style={{ marginTop: '1.5rem', textAlign: 'center' }}>
            <a
              href="#/admin/setup"
              style={{ color: '#4A90D9', fontSize: '0.8125rem', fontWeight: 500 }}
            >
              + Create new quiz
            </a>
          </div>
          <div style={{ marginTop: '1rem', textAlign: 'center' }}>
            <button onClick={logout} style={linkButtonStyle}>Sign out</button>
          </div>
        </div>
      </div>
    );
  }

  return <LoadingScreen />;
}

function LoadingScreen() {
  return (
    <div style={centerStyle}>
      <div style={{ textAlign: 'center' }}>
        <div style={{
          width: '32px',
          height: '32px',
          border: '3px solid #E5E7EB',
          borderTopColor: '#041E42',
          borderRadius: '50%',
          animation: 'admin-spin 0.8s linear infinite',
          margin: '0 auto 1rem',
        }} />
        <p style={{ color: '#54585A', fontSize: '0.875rem' }}>Loading...</p>
        <style>{`@keyframes admin-spin { to { transform: rotate(360deg); } }`}</style>
      </div>
    </div>
  );
}

const centerStyle = {
  minHeight: '100vh',
  backgroundColor: '#F5F5F7',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  fontFamily: 'system-ui, -apple-system, sans-serif',
  padding: '1rem',
};

const cardStyle = {
  backgroundColor: '#FFFFFF',
  borderRadius: '12px',
  padding: '2.5rem',
  textAlign: 'center',
  maxWidth: '400px',
  width: '100%',
  boxShadow: '0 4px 24px rgba(0, 0, 0, 0.08)',
};

const codeStyle = {
  backgroundColor: '#F3F4F6',
  padding: '2px 6px',
  borderRadius: '4px',
  fontSize: '0.8125rem',
};

const linkButtonStyle = {
  background: 'none',
  border: 'none',
  color: '#041E42',
  fontSize: '0.875rem',
  cursor: 'pointer',
  textDecoration: 'underline',
  fontFamily: 'system-ui, -apple-system, sans-serif',
};

export default AdminApp;
