import React, { useState, useEffect } from 'react';
import { supabase } from '../../supabaseClient';

const styles = {
  card: {
    background: '#FFFFFF',
    borderRadius: '8px',
    padding: '24px',
    border: '1px solid #E5E7EB',
  },
  statCard: {
    background: '#FFFFFF',
    borderRadius: '8px',
    padding: '20px',
    border: '1px solid #E5E7EB',
    textAlign: 'center',
  },
  sectionTitle: {
    color: '#041E42',
    fontSize: '1rem',
    fontWeight: 600,
    marginBottom: '16px',
    marginTop: 0,
  },
  statValue: {
    fontSize: '2rem',
    fontWeight: 700,
    color: '#041E42',
    lineHeight: 1.2,
  },
  statLabel: {
    fontSize: '0.8125rem',
    color: '#54585A',
    marginTop: '4px',
  },
  emptyState: {
    textAlign: 'center',
    padding: '48px 24px',
    color: '#54585A',
  },
};

function formatPct(n) {
  return n != null ? Math.round(n) + '%' : '';
}

function BarChart({ data, maxVal, colorFn, labelKey, valueKey, valueSuffix }) {
  if (!data || data.length === 0) return <div style={{ color: '#9CA3AF', fontSize: '0.875rem' }}>No data</div>;
  const max = maxVal || Math.max(...data.map((d) => d[valueKey] || 0), 1);
  return (
    <div>
      {data.map((item, i) => (
        <div key={i} style={{ display: 'flex', alignItems: 'center', gap: '12px', marginBottom: '8px' }}>
          <div style={{ width: '100px', fontSize: '0.8125rem', color: '#374151', textAlign: 'right', flexShrink: 0, overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
            {item[labelKey]}
          </div>
          <div style={{ flex: 1, backgroundColor: '#F3F4F6', borderRadius: '4px', height: '24px', position: 'relative', overflow: 'hidden' }}>
            <div
              style={{
                width: `${Math.max((item[valueKey] / max) * 100, 2)}%`,
                height: '100%',
                backgroundColor: colorFn ? colorFn(item, i) : '#4A90D9',
                borderRadius: '4px',
                transition: 'width 0.3s ease',
              }}
            />
          </div>
          <div style={{ width: '60px', fontSize: '0.8125rem', color: '#374151', fontWeight: 500 }}>
            {item[valueKey]}{valueSuffix || ''}
          </div>
        </div>
      ))}
    </div>
  );
}

function VerticalBarChart({ data, barColor }) {
  if (!data || data.length === 0) return null;
  const maxGames = Math.max(...data.map((d) => d.games), 1);
  const chartHeight = 160;
  return (
    <div>
      <div style={{ display: 'flex', alignItems: 'flex-end', gap: '2px', height: chartHeight + 'px', borderBottom: '1px solid #E5E7EB', paddingBottom: '1px' }}>
        {data.map((d, i) => {
          const h = Math.max((d.games / maxGames) * chartHeight, 2);
          return (
            <div key={i} style={{ flex: 1, display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'flex-end', height: '100%' }} title={`${d.day}: ${d.games} games, avg ${formatPct(d.avg_score)}`}>
              <div style={{ fontSize: '0.625rem', color: '#6B7280', marginBottom: '2px' }}>
                {d.games > 0 ? d.games : ''}
              </div>
              <div
                style={{
                  width: '100%',
                  maxWidth: '20px',
                  height: h + 'px',
                  backgroundColor: barColor || '#4A90D9',
                  borderRadius: '2px 2px 0 0',
                  minWidth: '4px',
                }}
              />
            </div>
          );
        })}
      </div>
      <div style={{ display: 'flex', gap: '2px', marginTop: '4px' }}>
        {data.map((d, i) => {
          const showLabel = i === 0 || i === data.length - 1 || i === Math.floor(data.length / 2);
          return (
            <div key={i} style={{ flex: 1, textAlign: 'center', fontSize: '0.5625rem', color: '#9CA3AF', overflow: 'hidden' }}>
              {showLabel ? d.day.slice(5) : ''}
            </div>
          );
        })}
      </div>
    </div>
  );
}

function AnalyticsTab({ community }) {
  const [loading, setLoading] = useState(true);
  const [stats, setStats] = useState({ totalGames: 0, avgScore: 0, uniquePlayers: 0, questionCount: 0 });
  const [dailyGames, setDailyGames] = useState([]);
  const [scoreDist, setScoreDist] = useState([]);
  const [categoryStats, setCategoryStats] = useState([]);
  const [hardest, setHardest] = useState([]);
  const [easiest, setEasiest] = useState([]);
  const [questionsByCategory, setQuestionsByCategory] = useState([]);
  const [questionsByDifficulty, setQuestionsByDifficulty] = useState([]);

  useEffect(() => {
    loadAnalytics();
  }, [community.id]); // eslint-disable-line

  async function loadAnalytics() {
    setLoading(true);
    const cid = community.id;
    const thirtyDaysAgo = new Date(Date.now() - 30 * 24 * 60 * 60 * 1000).toISOString();

    // Run queries in parallel
    const [gamesRes, allGamesRes, questionsRes, answersRes] = await Promise.all([
      // Games in last 30 days
      supabase
        .from('games')
        .select('created_at, score, total_questions, user_id')
        .eq('community_id', cid)
        .gte('created_at', thirtyDaysAgo)
        .order('created_at', { ascending: true }),
      // All games for overall stats
      supabase
        .from('games')
        .select('score, total_questions, user_id')
        .eq('community_id', cid),
      // Question bank
      supabase
        .from('community_questions')
        .select('id, category, difficulty, tags')
        .eq('community_id', cid),
      // Game answers with question details
      supabase
        .from('game_answers')
        .select('question_id, is_correct, question_text, correct_answer, category')
        .eq('community_id', cid),
    ]);

    const recentGames = gamesRes.data || [];
    const allGames = allGamesRes.data || [];
    const questions = questionsRes.data || [];
    const answers = answersRes.data || [];

    // Top-level stats
    const totalGames = allGames.length;
    const avgScore = totalGames > 0
      ? allGames.reduce((sum, g) => sum + (g.total_questions > 0 ? (g.score / g.total_questions) * 100 : 0), 0) / totalGames
      : 0;
    const uniquePlayers = new Set(allGames.map((g) => g.user_id).filter(Boolean)).size;
    setStats({ totalGames, avgScore, uniquePlayers, questionCount: questions.length });

    // Daily games (last 30 days)
    const dayMap = {};
    for (let i = 29; i >= 0; i--) {
      const d = new Date(Date.now() - i * 24 * 60 * 60 * 1000);
      const key = d.toISOString().slice(0, 10);
      dayMap[key] = { day: key, games: 0, totalScore: 0 };
    }
    recentGames.forEach((g) => {
      const day = g.created_at.slice(0, 10);
      if (dayMap[day]) {
        dayMap[day].games++;
        dayMap[day].totalScore += g.total_questions > 0 ? (g.score / g.total_questions) * 100 : 0;
      }
    });
    const daily = Object.values(dayMap).map((d) => ({
      ...d,
      avg_score: d.games > 0 ? d.totalScore / d.games : null,
    }));
    setDailyGames(daily);

    // Score distribution
    const brackets = { '90-100%': 0, '70-89%': 0, '50-69%': 0, 'Below 50%': 0 };
    allGames.forEach((g) => {
      const pct = g.total_questions > 0 ? (g.score / g.total_questions) : 0;
      if (pct >= 0.9) brackets['90-100%']++;
      else if (pct >= 0.7) brackets['70-89%']++;
      else if (pct >= 0.5) brackets['50-69%']++;
      else brackets['Below 50%']++;
    });
    setScoreDist(Object.entries(brackets).map(([label, count]) => ({ label, count })));

    // Questions by category and difficulty
    const catMap = {};
    const diffMap = {};
    questions.forEach((q) => {
      const cat = q.category || 'Uncategorized';
      catMap[cat] = (catMap[cat] || 0) + 1;
      const diff = q.difficulty || 'unset';
      diffMap[diff] = (diffMap[diff] || 0) + 1;
    });
    setQuestionsByCategory(
      Object.entries(catMap)
        .map(([label, count]) => ({ label, count }))
        .sort((a, b) => b.count - a.count)
    );
    setQuestionsByDifficulty(
      Object.entries(diffMap)
        .map(([label, count]) => ({ label, count }))
        .sort((a, b) => b.count - a.count)
    );

    // Category performance from answers
    if (answers.length > 0) {
      const catPerf = {};
      answers.forEach((a) => {
        const cat = a.category || 'Uncategorized';
        if (!catPerf[cat]) catPerf[cat] = { total: 0, correct: 0 };
        catPerf[cat].total++;
        if (a.is_correct) catPerf[cat].correct++;
      });
      setCategoryStats(
        Object.entries(catPerf)
          .map(([label, { total, correct }]) => ({
            label,
            total,
            correct,
            rate: Math.round((correct / total) * 100),
          }))
          .sort((a, b) => b.total - a.total)
      );

      // Hardest / easiest questions
      const qPerf = {};
      answers.forEach((a) => {
        const qid = a.question_id;
        if (!qPerf[qid]) qPerf[qid] = { total: 0, correct: 0, text: a.question_text || `Q#${qid?.slice(0, 6)}` };
        qPerf[qid].total++;
        if (a.is_correct) qPerf[qid].correct++;
      });
      const ranked = Object.values(qPerf)
        .filter((q) => q.total >= 3)
        .map((q) => ({ ...q, rate: Math.round((q.correct / q.total) * 100) }))
        .sort((a, b) => a.rate - b.rate);
      setHardest(ranked.slice(0, 5));
      setEasiest([...ranked].sort((a, b) => b.rate - a.rate).slice(0, 5));
    } else {
      setCategoryStats([]);
      setHardest([]);
      setEasiest([]);
    }

    setLoading(false);
  }

  if (loading) {
    return (
      <div>
        <h1 style={{ color: '#041E42', fontSize: '1.5rem', marginBottom: '0.5rem', fontWeight: 600 }}>Analytics</h1>
        <p style={{ color: '#54585A' }}>Loading analytics for {community.name}...</p>
      </div>
    );
  }

  if (stats.totalGames === 0 && stats.questionCount === 0) {
    return (
      <div>
        <h1 style={{ color: '#041E42', fontSize: '1.5rem', marginBottom: '0.5rem', fontWeight: 600 }}>Analytics</h1>
        <p style={{ color: '#54585A', marginBottom: '2rem' }}>Performance insights for {community.name}</p>
        <div style={{ ...styles.card, ...styles.emptyState }}>
          <div style={{ fontSize: '3rem', marginBottom: '16px' }}></div>
          <h3 style={{ color: '#041E42', marginBottom: '8px' }}>No data yet</h3>
          <p style={{ fontSize: '0.9375rem', maxWidth: '400px', margin: '0 auto' }}>
            Once players start taking quizzes through your embed, you'll see performance trends,
            player activity, and question stats here.
          </p>
        </div>
      </div>
    );
  }

  const scoreColor = (val) => {
    if (val >= 80) return '#059669';
    if (val >= 60) return '#D97706';
    return '#DC2626';
  };

  const bracketColor = (label) => {
    if (label === '90-100%') return '#059669';
    if (label === '70-89%') return '#10B981';
    if (label === '50-69%') return '#F59E0B';
    return '#EF4444';
  };

  return (
    <div>
      <h1 style={{ color: '#041E42', fontSize: '1.5rem', marginBottom: '0.5rem', fontWeight: 600 }}>Analytics</h1>
      <p style={{ color: '#54585A', marginBottom: '2rem' }}>Performance insights for {community.name}</p>

      {/* Stat cards */}
      <div style={{ display: 'grid', gridTemplateColumns: 'repeat(4, 1fr)', gap: '16px', marginBottom: '24px' }}>
        <div style={styles.statCard}>
          <div style={styles.statValue}>{stats.totalGames}</div>
          <div style={styles.statLabel}>Total Games</div>
        </div>
        <div style={styles.statCard}>
          <div style={{ ...styles.statValue, color: scoreColor(stats.avgScore) }}>
            {formatPct(stats.avgScore)}
          </div>
          <div style={styles.statLabel}>Avg Score</div>
        </div>
        <div style={styles.statCard}>
          <div style={styles.statValue}>{stats.uniquePlayers}</div>
          <div style={styles.statLabel}>Unique Players</div>
        </div>
        <div style={styles.statCard}>
          <div style={styles.statValue}>{stats.questionCount}</div>
          <div style={styles.statLabel}>Questions in Bank</div>
        </div>
      </div>

      {/* Games over time */}
      <div style={{ ...styles.card, marginBottom: '20px' }}>
        <h3 style={styles.sectionTitle}>Games Played (Last 30 Days)</h3>
        {dailyGames.some((d) => d.games > 0) ? (
          <VerticalBarChart data={dailyGames} barColor="#4A90D9" />
        ) : (
          <div style={{ color: '#9CA3AF', fontSize: '0.875rem', padding: '20px 0', textAlign: 'center' }}>
            No games in the last 30 days
          </div>
        )}
      </div>

      <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px', marginBottom: '20px' }}>
        {/* Score Distribution */}
        <div style={styles.card}>
          <h3 style={styles.sectionTitle}>Score Distribution</h3>
          <BarChart
            data={scoreDist}
            labelKey="label"
            valueKey="count"
            colorFn={(item) => bracketColor(item.label)}
          />
        </div>

        {/* Category Performance */}
        <div style={styles.card}>
          <h3 style={styles.sectionTitle}>Category Performance</h3>
          {categoryStats.length > 0 ? (
            <div>
              {categoryStats.map((cat, i) => (
                <div key={i} style={{ display: 'flex', alignItems: 'center', gap: '12px', marginBottom: '8px' }}>
                  <div style={{ width: '100px', fontSize: '0.8125rem', color: '#374151', textAlign: 'right', flexShrink: 0, overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                    {cat.label}
                  </div>
                  <div style={{ flex: 1, backgroundColor: '#F3F4F6', borderRadius: '4px', height: '24px', position: 'relative', overflow: 'hidden' }}>
                    <div
                      style={{
                        width: `${cat.rate}%`,
                        height: '100%',
                        backgroundColor: scoreColor(cat.rate),
                        borderRadius: '4px',
                      }}
                    />
                  </div>
                  <div style={{ width: '60px', fontSize: '0.8125rem', color: '#374151', fontWeight: 500 }}>
                    {cat.rate}%
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div style={{ color: '#9CA3AF', fontSize: '0.875rem' }}>No answer data yet</div>
          )}
        </div>
      </div>

      <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px', marginBottom: '20px' }}>
        {/* Questions by Category */}
        <div style={styles.card}>
          <h3 style={styles.sectionTitle}>Questions by Category</h3>
          <BarChart
            data={questionsByCategory}
            labelKey="label"
            valueKey="count"
            colorFn={() => '#4A90D9'}
          />
        </div>

        {/* Questions by Difficulty */}
        <div style={styles.card}>
          <h3 style={styles.sectionTitle}>Questions by Difficulty</h3>
          <BarChart
            data={questionsByDifficulty}
            labelKey="label"
            valueKey="count"
            colorFn={(item) => {
              const d = item.label.toLowerCase();
              if (d === 'easy') return '#10B981';
              if (d === 'medium') return '#F59E0B';
              if (d === 'hard') return '#EF4444';
              return '#9CA3AF';
            }}
          />
        </div>
      </div>

      {/* Hardest / Easiest Questions */}
      {(hardest.length > 0 || easiest.length > 0) && (
        <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px' }}>
          <div style={styles.card}>
            <h3 style={styles.sectionTitle}>Hardest Questions</h3>
            {hardest.length > 0 ? (
              hardest.map((q, i) => (
                <div key={i} style={{ display: 'flex', alignItems: 'center', gap: '12px', marginBottom: '10px', paddingBottom: '10px', borderBottom: i < hardest.length - 1 ? '1px solid #F3F4F6' : 'none' }}>
                  <div style={{ width: '44px', height: '44px', borderRadius: '8px', backgroundColor: '#FEE2E2', display: 'flex', alignItems: 'center', justifyContent: 'center', fontWeight: 700, color: '#DC2626', fontSize: '0.875rem', flexShrink: 0 }}>
                    {q.rate}%
                  </div>
                  <div style={{ flex: 1, minWidth: 0 }}>
                    <div style={{ fontSize: '0.8125rem', color: '#1F2937', overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                      {q.text}
                    </div>
                    <div style={{ fontSize: '0.6875rem', color: '#9CA3AF' }}>
                      {q.total} attempts
                    </div>
                  </div>
                </div>
              ))
            ) : (
              <div style={{ color: '#9CA3AF', fontSize: '0.875rem' }}>Not enough data (min 3 attempts)</div>
            )}
          </div>

          <div style={styles.card}>
            <h3 style={styles.sectionTitle}>Easiest Questions</h3>
            {easiest.length > 0 ? (
              easiest.map((q, i) => (
                <div key={i} style={{ display: 'flex', alignItems: 'center', gap: '12px', marginBottom: '10px', paddingBottom: '10px', borderBottom: i < easiest.length - 1 ? '1px solid #F3F4F6' : 'none' }}>
                  <div style={{ width: '44px', height: '44px', borderRadius: '8px', backgroundColor: '#D1FAE5', display: 'flex', alignItems: 'center', justifyContent: 'center', fontWeight: 700, color: '#059669', fontSize: '0.875rem', flexShrink: 0 }}>
                    {q.rate}%
                  </div>
                  <div style={{ flex: 1, minWidth: 0 }}>
                    <div style={{ fontSize: '0.8125rem', color: '#1F2937', overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                      {q.text}
                    </div>
                    <div style={{ fontSize: '0.6875rem', color: '#9CA3AF' }}>
                      {q.total} attempts
                    </div>
                  </div>
                </div>
              ))
            ) : (
              <div style={{ color: '#9CA3AF', fontSize: '0.875rem' }}>Not enough data (min 3 attempts)</div>
            )}
          </div>
        </div>
      )}
    </div>
  );
}

export default AnalyticsTab;
